<template>
  <div class="adjust-risk-level">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      class="form-content"
      :label-width="120"
    >
      <FormItem label="调整的业务名称">
        <label>{{dataObj.busName}}</label>
        <label class="speclial-label" @click="assessDetail">评估详情</label>
      </FormItem>
      <FormItem label="当前风险等级">
        <label v-tdloss="{code:dataObj.riskLevel,list:adjustLevels}"></label>
      </FormItem>
      <Row>
        <Col span="11">
          <FormItem label="流程标题" prop="taskName">
              <Input v-model="formValidate.taskName" placeholder="请输入流程标题"></Input>
          </FormItem>
        </Col>
      </Row>
      <Row>
        <Col span="11">
          <FormItem label="调整的等级" prop="adjustLevel">
            <Select v-model="formValidate.adjustLevel">
                <Option v-for="item in adjustLevels" :value="item.levelCode" :key="item.levelCode">{{item.levelName}}</Option>
            </Select>
          </FormItem>
        </Col>
        <Col span="2">
          <span class="speical-span">
            <FormItem></FormItem>
          </span>
        </Col>
        <Col span="11">
          <FormItem label="下次评估时间">
            <DatePicker type="date" placeholder="请选择下次评估时间" :value="formValidate.nextEvalDate" @on-change="formValidate.nextEvalDate=$event"></DatePicker>
            <span style="font-size:14px">
              如果下次评估时间为空，则由系统自行根据
              <label class="spanBox">
                <Poptip placement="top" width="200" trigger="hover">
                  规则
                  <div class="api" slot="content">
                    <table style="color:#666">
                      <tr>
                        <td>风险等级</td>
                        <td>评估周期（月）</td>
                      </tr>
                      <tr v-for="item in adjustLevels">
                        <td>{{item.levelName}}</td>
                        <td>{{item.evalCycle}}</td>
                      </tr>
                    </table>
                  </div>
                </Poptip>
              </label>计算
            </span>
          </FormItem>
        </Col>
      </Row>
      <!-- <FormItem label="调整事由" prop="causeType" class="special-tip">
        <Select v-model="formValidate.causeType">
            <Option v-for="item in causeTypes" :value="item.code" :key="item.code">{{item.name}}</Option>
        </Select>
        <span style="font-size:14px">
          事由类别不足前往
          <label class="spanBox" @click="add">添加</label>
        </span>
      </FormItem> -->
      <FormItem label="事由说明">
        <Input
          v-model="formValidate.causeDetail"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder="请输入事由说明"
        ></Input>
      </FormItem>
       <Row>
            <Form-item label="附件" class="form-item" style="width:100%">
                <Upload
                ref="upload"
                multiple
                type="drag"
                :show-upload-list="false"
                :default-file-list="defaultList"
                :before-upload="handleUploadfile"
                :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
                :max-size="10240"
                :on-format-error="handleFormatError"
                :on-exceeded-size="handleMaxSize"
                :action="axios.defaults.baseURL+'/system/remote/micUpload'"
                :on-success="handleSuccess"
                :on-error="handleError"
              >
                <div style="padding: 20px 0">
                  <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                  <p>请上传附件</p>
                </div>
              </Upload>
              <div>上传格式为zip、rar、xls、xlsx、doc、docx、pdf，且大小不超过10MB</div>
            </Form-item>
            <Form-item label=""  class="form-item" style="width:100%">
                <Table border :columns="columns1" :data="uploadList"></Table>
            </Form-item>
      </Row>
      <FormItem>
        <div class="button-content">
          <Button type="primary" @click="next('formValidate')">下一步</Button>
          <Button @click="handleCancel">取消</Button>
        </div>
      </FormItem>
       <Modal :mask-closable="false"   v-model="modal1" width="360" class="treeBoxModel">
          <p slot="header" style="color:#f60;text-align:center">
            <span>审核人信息</span>
          </p>
          <div style="text-align:left;overflow-y:auto;height:300px">
            <FormItem prop="nextUser">
              <Tree
                ref="treeRef"
                :data="data2"
                show-checkbox
                multiple
                @on-check-change="updateNextUser"
              ></Tree>
            </FormItem>
          </div>
          <div slot="footer">
            <Button type="primary" size="large" long @click="handleSubmit('formValidate')">立即申请</Button>
          </div>
        </Modal>
    </Form>
  </div>
</template>
<script>
export default {
  props: ["dataObj"],
  created() {
        let url = `/bpm/getNodeDefDetail?bpmType=adj_busi_risk`;
        this.formValidate.busId = this.dataObj.id;
        this.formValidate.busName = this.dataObj.busName;
        this.formValidate.evalId = this.dataObj.evalId;
        this.formValidate.currLevel = this.dataObj.riskLevel;
        this.axios
        .get(url)
        .then((response) =>{
            let data = {
                bpmName: response.data[1].bpmName,
                nodeId:  response.data[1].nodeId,
                roleName: response.data[1].roleName
            };
            this.formValidate.bpmName = data.bpmName;
            data = JSON.stringify(data);
            this.axios
            .post("/bpm/getNextRole", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(res => {
              if (res.code == "200") {
                    this.data2 = res.data;
                    this.treeNLeafNodeDisable(this.data2);
              }
            })
            .catch(error => {});
      })
      .catch(function(error) {});
        this.axios
        .post("/risk/busi/getRiskLevel")
        .then(response=> {
            if (response.code == "200") {
                this.adjustLevels = response.data;
            }
        })
        .catch(function(error) {});
        this.axios
        .get(`/system/getDic/ADJ_TYPE`)
        .then(response => {
            if (response.code == "200") {
               this.causeTypes = response.data.ADJ_TYPE;
            }
        })
        .catch(error => {});
        if (this.dataObj.adjStatus == "3"){
              let data = JSON.stringify({
                busId: this.dataObj.id,
                adjStatus: this.dataObj.adjStatus
              });
              this.axios
              .post("/risk/busi/selectBusAdjInfo", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                  if (response.code == "200") {
                      this.formValidate.nextEvalDate = response.data.nextEvalDate;
                      this.formValidate.causeDetail =  response.data.causeDetail;
                      this.formValidate.adjustLevel = response.data.adjustLevel;
                      this.formValidate.businessId = response.data.businessId;
                      this.formValidate.taskName = response.data.taskName;
                      this.axios
                      .post(`/system/remote/getFileList/${response.data.businessId}`)
                      .then(res =>{
                            if (res.code == "200"){
                                let fileList = res.data;
                                fileList.forEach(element => {
                                     this.defaultList.push({
                                        url: element.fileId,
                                        name: element.filename
                                      });
                                });
                            }
                      })
                      .catch(function(error) {});
                  }
              })
        }
  },
  beforeUpdate() {
    this.uploadList = this.$refs.upload.fileList;
  },
  data() {
    const validaterNextUser = (rule, value, callback) => {
      if (this.formValidate.nextUser == "" && this.modal1) {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    return {
      modal1:false,
      formValidate: {
        nextUser: "",
        nextEvalDate: "",
        causeDetail: "",
        currLevel: "",
        adjustLevel: "",
        bpmName: "",
        busId: "",
        evalId: "",
        taskName:""//流程标题
      },
      uploadList: [],
      adjustLevels: [],
      causeTypes: [],
      data2: [],
      defaultList: [
      ],
      columns1: [
        {
          title: '文件名',
          key: 'name'
        },
        {
            title: '操作',
            key: '',
            width: 150,
            align: 'center',
            render: (h, params) => {
                return h('div', [
                    h('Button', {
                        props: {
                            type: 'primary',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px'
                        },
                        on: {
                            click: () => {
                                this.fileDownload(params.index, params.row.name)
                            }
                        }
                    }, '下载'),
                    h('Button', {
                        props: {
                            type: 'error',
                            size: 'small'
                        },
                        on: {
                            click: () => {
                                this.remove(params.index)
                            }
                        }
                    }, '删除')
                ]);
            }
        }
      ],
      ruleValidate: {
       nextUser: [
          {
            required: false,
            validator: validaterNextUser,
            trigger: "on-check-change"
          }
        ],
        adjustLevel: [
          {
            required: true,
            message: "请选择调整的等级",
            trigger: "change"
          }
        ],
        causeDetail: [
          {
            type: "string",
            max: 512,
            message: "长度最大不能超过512",
            trigger: "blur"
          }
        ],
        taskName:[
          {
            required: true,
            message: "流程标题不能为空",
            trigger: "blur"
          },
          {
            type: "string",
            max: 50,
            message: "流程标题不能超过50个字",
            trigger: "blur"
          }
        ],
      }
    };
  },
  mounted() {
  },
  methods: {
    add() {
      let { href } = this.$router.resolve({
        name: "verCode"
      });
      window.open(href, "_blank");
    },
     treeNLeafNodeDisable(treeDatas){
        for(let i=0;i<treeDatas.length;i++){
            if (treeDatas[i].children){
                this.$set(treeDatas[i], "disabled", true);
                this.$set(treeDatas[i], "expand", true);
                this.treeNLeafNodeDisable(treeDatas[i].children);
            }
        }
    },
    updateNextUser(nodes, node) {
        nodes.forEach(el => {
            if (el.nodeKey != node.nodeKey){
                el.checked = false;
            }
          });
          this.formValidate.nextUser = "";
          nodes.forEach(element => {
            if (element.checked==true)
              this.formValidate.nextUser = element.code;
          });
          this.$refs["formValidate"].validateField("nextUser");
    },
     next(name) {
      this.$refs[name].validate(valid => {
        if (valid) this.modal1 = true;
        else this.$Message.error("校验失败!");
      });
    },
    handleFormatError (file) {
        this.$Notice.warning({
            title: '附件格式错误',
            desc: '请选择zip、rar、xls、xlsx、doc、docx、pdf.'
        });
    },
    handleMaxSize (file) {
        this.$Notice.warning({
            title: '超出了附件大小限制',
            desc: ' 不应超过10M'
        });
    },
    remove(index){
      let filePath = this.uploadList[index].url;
      this.axios
      .post(`/system/remote/delFile/${filePath}`)
      .then(response => {
          if ((response = true)) {
              this.uploadList.splice(index, 1);
              this.$Message.success("删除成功");
          }else {
              this.$Message.error("删除失败");
          }
      })
    },
    handleUploadfile(file){
       const check = this.uploadList.length < 5;
        if (!check) {
            this.$Notice.warning({
                title: '只能上传最多五个附件.'
            });
        }
        return check;
    },
    handleSuccess(response, file) {
      if (response.code == "200") {
          file.url = response.data[0].fileId;
      }
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    handleError() {
      this.$Message.error("上传失败");
    },
    showPrinciple() {},
    assessDetail() {
      let { href } = this.$router.resolve({
        name: "assessDetail2",
        query:{id:this.dataObj.id},
      });
      window.open(href, "_blank");
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
          if (valid) {
            this.formValidate.fileName = [];
            this.formValidate.filePath = [];
            this.uploadList.forEach(element => {
              this.formValidate.fileName.push(element.name);
              this.formValidate.filePath.push(element.url);
            });
            let data = JSON.parse(JSON.stringify(this.formValidate));
            data.fileName = JSON.stringify(
              data.fileName
            );
            data.filePath = JSON.stringify(
              data.filePath
            );
            data = JSON.stringify(data);
            this.axios
              .post("/risk/busi/busRiskAdjApp", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.success("保存成功");
                   setTimeout(()=>{
                     this.modal1 = false;
                     this.$emit("save");
                    },2000)
                }else {
                  this.$Message.error("保存失败");
                }
              })
              .catch(error => {});
          } else {
            this.$Message.error("校验失败!");
          }
      });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss" scoped>
.adjust-risk-level {
  margin: 0 30px;
  .form-content {
    .speical-span {
      width: 8.33333333%;
      float: left;
    }
    .ivu-date-picker {
      width: 100%;
    }
    .special-tip {
      .ivu-form-item-error-tip {
        margin-top: -12px;
      }
    }
  }
  label {
    font-size: 14px;
    &.speclial-label {
      margin-left: 20px;
      color: #0000cc;
      cursor: pointer;
    }
  }
}
</style>


