<template>
  <div class="second-step2 part-seconde-step2 ">
    <div class="content">
      <div class="table-info">
        <span>评估部门/单位：{{receiveDatas.orgName}}</span>
        <span>评估期间：{{receiveDatas.evalPeriod}}~{{receiveDatas.evalPeriod1}}</span>
      </div>
      <div class="target" ref="target">
        <span>指标</span>
        <Button
          v-for="(item,index) in activeButtoArr"
          :key="index"
          @click="activeButtoFun(item)"
        >{{item}}级</Button>
      </div>
      <div class="tab-desc">
        <span>{{indicatorName}}</span>
        <span v-tdloss="{code:orgRiskScore.contrLevel,list:riskLevels}"></span>
        <span>总分：{{scoreObject.contrScore}}分</span>
        <span style="margin-left:20px">风险等级：{{scoreObject.contrLevel}}</span>
      </div>
      <table class="tableBox" cellspacing="0" cellpadding="0" style="margin-bottom:0;">
        <thead :class="positonFix?' positon-fix pos-fix-thead':''">
          <tr>
            <td class="p1">项目</td>
            <td class="p2">权重</td>
            <td class="p3">得分</td>
            <td class="p4" style="height: 40px;line-height: 40px;text-align: center;">指标内容</td>
            <td class="p5">备注</td>
            <td class="p6">附件</td>
          </tr>
        </thead>
      </table>
      <div class="CollapseBox">
        <el-collapse v-model="activeNames" @change="handleChange">
          <template v-for="item in activeArr">
            <el-collapse-item :name="item.indNum" :key="item.indNum">
              <template slot="title">
                <p class="p1">
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.indicator.indicatorName"
                  >
                    <span
                      slot="reference"
                      class="textOverflow textMaxWidth"
                    >{{item.indNum}}{{item.indicator.indicatorName}}</span>
                  </el-popover>
                </p>
                <p class="p2 ppadding">{{item.indicator.weight}}</p>
                <p
                  class="p3 ppadding"
                >{{firstScore?item.indicator.defaultValue:scoreObject[item.indicator.indicatorCode]}}
                </p>
                <p class="p4 ppadding">
                </p>
                <p class="p5 ppadding">
                </p>
                <p class="p6 ppadding"></p>
              </template>
              <template v-if="item.sonVo.length>0?true:false">
                <el-collapse-item
                  v-for="(item2,index2) in item.sonVo"
                  :key="item2.indNum"
                  :name="item2.indNum"
                >
                  <template slot="title">
                <p class="p1 ppadding">
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item2.indicator.indicatorName"
                  >
                    <span
                      slot="reference"
                      class="textOverflow textMaxWidth"
                    >{{item2.indNum}}{{item2.indicator.indicatorName}}</span>
                  </el-popover>
                </p>
                    <p class="p2 ppadding">{{item2.indicator.weight}}</p>
                    <p
                      class="p3 ppadding"
                    >{{firstScore?item2.indicator.defaultValue:scoreObject[item2.indicator.indicatorCode]}}</p>
                    <p class="p4 ppadding">
                    </p>

                    <p class="p5 ppadding">
                    </p>
                    <p class="p6 ppadding"></p>
                  </template>
                  <template v-if="item2.sonVo.length>0?true:false">
                    <el-collapse-item
                      v-for="(item3,index3) in item2.sonVo"
                      :key="item3.indNum"
                      :name="item3.indNum"
                    >
                      <template slot="title">
                        <p class="p1">
                          <el-popover
                            placement="top-start"
                            title
                            width="200"
                            trigger="hover"
                            :content="item3.indicator.indicatorName"
                          >
                            <span
                              slot="reference"
                              class="textOverflow textMaxWidth"
                            >{{item3.indNum}}{{item3.indicator.indicatorName}}</span>
                          </el-popover>
                        </p>
                        <p class="p2 ppadding">{{item3.indicator.weight}}</p>
                        <p
                          class="p3 ppadding"
                        >{{firstScore?item3.indicator.defaultValue:scoreObject[item3.indicator.indicatorCode]}}</p>
                        <p class="p4 ppadding">
                        </p>

                        <p class="p5 ppadding">
                        </p>
                        <p class="p6 ppadding"></p>
                      </template>
                      <template v-if="item3.sonVo.length>0?true:false">
                        <el-collapse-item
                          v-for="(item4,index4) in item3.sonVo"
                          :key="item4.indNum"
                          :name="item4.indNum"
                        >
                          <template slot="title">
                            <p class="p1">
                              <el-popover
                                placement="top-start"
                                title
                                width="200"
                                trigger="hover"
                                :content="item4.indicator.indicatorName"
                              >
                                <span
                                  slot="reference"
                                  class="textOverflow textMaxWidth"
                                >{{item4.indNum}}{{item4.indicator.indicatorName}}</span>
                              </el-popover>
                            </p>
                            <p class="p2 ppadding">{{item4.indicator.weight}}</p>
                            <p
                              class="p3 ppadding"
                            >{{firstScore?item4.indicator.defaultValue:scoreObject[item4.indicator.indicatorCode]}}</p>
                            <p class="p4 ppadding">
                              <RadioGroup v-model="item4.indicator.optId" @on-change="changRadio(item4.indicator.optId, item4 )">
                                <Radio v-for="(item8, index8) in item4.indicator.options" :label="item8.optId" :key="item8.optId">{{item8.enums+'('+item8.score+')'}}</Radio>
                              </RadioGroup>
                            </p>
                            <p class="p5 ppadding">
                              <Input
                                v-model="item4.indicator.detailRemark"
                                @on-change="updatedetailRemark(item4.indicator.indicatorCode,item4.indicator.remark)"
                              ></Input>
                            </p>
                            <p
                              class="p13 ppadding"
                              @click="watchFiles(item4,item4.indicator.indicatorCode)"
                              style="color: #3399ff"
                            >上传</p>
                          </template>
                        </el-collapse-item>
                      </template>
                    </el-collapse-item>
                  </template>
                </el-collapse-item>
              </template>
            </el-collapse-item>
          </template>
        </el-collapse>
      </div>
      <Modal :mask-closable="false" v-model="modal1" title="错误列表">
        <div class="fujian">
          <div v-for="(item,index) in errorList" :key="index">
            <span
              style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
              class="cursorPointer"
            >{{item.message}}</span>
          </div>
        </div>
        <div slot="footer"></div>
      </Modal>
      <Modal :mask-closable="false"  
      v-model="showModal"
      title="文件列表"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: '40%'}"
    >
      <component
        :is="tabview"
        :file-obj="fileObj"
        :key="fileId"
        @requestUploadList="receiveUploadList"
      ></component>
      <div slot="footer"></div>
    </Modal>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  components:{showUsualFiles},
  props: {
    receiveDatas: Object
  },
  created() {
    this.getOrgContr();
    this.getContrDetailByLevelNum();
  },
  mounted(){
    console.log('mounted');
    this.$nextTick(() => {
      window.addEventListener('scroll', this.changeScrol, true); 
    })
  },
  destroyed() {
    console.log('destroyed');
    //离开该页面需要移除这个监听的事件
    window.removeEventListener("scroll", this.changeScrol,true);
  },
  data() {
    return {
      positonFix:false,
      optVal:1,
      showModal:false,
      fileId:"",
      tabview:'showUsualFiles',
      fileObj:{},
      modal1: false,
      errorList: [],
      indicatorName: null,
      firstScore: true,
      activeButtoArr: [],
      activeNames: [],
      activeArr: [],
      scoreObject: {},//总分和风险等级
      riskLevels: [],
      orgRiskScore: {
        orgId: "",
        modelId: "",
        modelCode: "",
        contrLevel: null,
        scoreDto: []
      },
      tableHeader: [
        { name: "指标项" },
        { name: "权重%" },
        { name: "指标分值" },
        { name: "系统指标内容" },
        { name: "备注" },
        { name: "附件%" }
      ]
    };
  },
  methods: {
    changeScrol() {
      /**
       * getBoundingClientRect().top 获取某元素距离浏览器顶部的高度，不包含滚动的距离
        this.offsetTop 表示的是吸顶元素距离顶部的条件值（一般项目需求是0）
       */
      let targetOffsetTop = this.$refs.target.getBoundingClientRect().top;
      console.log('targetOffsetTop', targetOffsetTop);
      if(targetOffsetTop < 200){
        this.positonFix = true;
      }else{
        this.positonFix = false;
      }
    },
    getContrDetailByLevelNum(){
      this.axios
      .post("/risk/org/getContrDetailByLevelNum")
      .then(response => {
        if (response.code == "200") {
          this.activeButtoArr = response.data;
          this.activeButtoFun('4');
        }
      })
      .catch(error => {});
    },
    getOrgContr(){
      this.axios
      .post(`/risk/org/getOrgContr?id=${this.receiveDatas.id}`)
      .then(response => {
        if (response.code == "200") {
          this.activeArr = response.data.indicatorVo;
          let orgInfo = response.data.orgInfo;
          let orgEvalModel = response.data.orgEvalModel;
          if (orgInfo) {
            this.scoreObject.contrScore= orgInfo.contrScore;
            this.scoreObject.contrLevel = orgInfo.contrLevel;
          }
          this.indicatorName = orgEvalModel.modelName;
          this.orgRiskScore.orgId = this.receiveDatas.id;
          this.orgRiskScore.modelId = orgEvalModel.id;
          this.orgRiskScore.modelCode = orgEvalModel.modelCode;
          // this.showTree(that.activeArr);
        }
      })
      .catch(error => {});
    },
    cancel(){},
    getFileTree(datas, code, uploadList) {
      datas.forEach(data => {
        if (data.indicator.hasSon == "1") {
          if (data.indicator.indicatorCode == code) {
            data.indicator.filePath = JSON.stringify(uploadList.filePath);
            data.indicator.fileName = JSON.stringify(uploadList.fileName);
            console.log("this.activeArr", this.activeArr);
            try {
              throw Error();
            } catch (error) {
              console.log("跳出");
            }
          } else {
            this.getFileTree(data.sonVo, code, uploadList);
          }
        } else {
          if (data.indicator.indicatorCode == code) {
            data.indicator.filePath = JSON.stringify(uploadList.filePath);
            data.indicator.fileName = JSON.stringify(uploadList.fileName);
            console.log("this.activeArr", this.activeArr);
            try {
              throw Error();
            } catch (error) {
              console.log("跳出");
            }
          }
        }
      });
    },
    receiveUploadList(list) {
      this.uploadList = {
        fileName: [],
        filePath: []
      };
      list.forEach(item => {
        this.uploadList.filePath.push(item.url);
        this.uploadList.fileName.push(item.name);
      });
      this.getFileTree(this.activeArr, this.fileCode, this.uploadList);
      console.log("this.activeArr", this.activeArr);
    },
    watchFiles(item, code) {
      this.fileCode = code;
      this.tabview = "showUsualFiles";
      this.showModal = true;
      this.fileId = item.indicator.indicatorCode;
      if (item.indicator && item.indicator.fileName) {
        let fileName = JSON.parse(item.indicator.fileName);
        let filePath = JSON.parse(item.indicator.filePath);
        let fileList = [];
        for (let i = 0; i < fileName.length; i++) {
          fileList.push({
            url: filePath[i],
            name: fileName[i]
          });
        }
        this.fileObj = {
          data: item,
          showFlag: true,
          fileList: fileList
        };
      } else {
        item.busId = item.indicator.fileBussiness;
        this.fileObj = {
          data: item,
          showFlag: true
        };
      }
    },
    handleSave(param) {
      this.orgRiskScore.scoreDto = [];
      this.showTree(this.activeArr);
      return new Promise((resolve, reject) =>{
        this.axios
          .post("/risk/org/saveContrRiskDetail", this.orgRiskScore, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              debugger
              if(param != 'nextStep'){
                  this.$Message.success("保存成功");
                }
                resolve(response);
            } else {
              reject();
              this.$Message.error("保存失败");
            }
          })
          .catch(error => {
            reject();
          });
      })
    },
    showTree(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].indicator.hasSon == "1") {
          this.showTree(treeDatas[i].sonVo);
        } else {
          let options = treeDatas[i].indicator.options;
          let ooindex = -1;
          options.forEach((element, ind) => {
            if (element.checked) ooindex = ind;
          });
          let scoreDto = {
            indicatorCode: treeDatas[i].indicator.indicatorCode,
            optId: treeDatas[i].indicator.optId,
            optName: treeDatas[i].indicator.optName,
            score: treeDatas[i].indicator.score,
            remark: treeDatas[i].indicator.remark,
            filePath: treeDatas[i].indicator.filePath,
            fileName: treeDatas[i].indicator.fileName,
          };
          this.orgRiskScore.scoreDto.push(scoreDto);
        }
      }
    },
    handleChange() {},
    updatedetailRemark(indicatorCode, remark) {
      this.orgRiskScore.scoreDto.forEach(element => {
        if (element.indicatorCode == indicatorCode) {
          element.remark = remark;
        }
      });
    },
    activeButtoFun(a) {
      let that = this;
      this.axios
        .post(`/risk/org/getContrDetailByLevel/${a}`)
        .then(response => {
          if (response.code == "200") {
            that.activeNames = response.data;
          }
        })
        .catch(error => {});
    },
    changRadio(val, item) {
      debugger
      item.indicator.options.forEach((orgOption, index) => {
        if (orgOption.optId == val) {
          item.indicator.remark = orgOption.optRemark;
          item.indicator.score = orgOption.score;
          item.indicator.optName = orgOption.enums;
          this.$forceUpdate();
          try {
            throw Error();
          } catch (error) {
            console.log("跳出");
          }
        }
      });
      this.sendBusRiskScore();
    },
    sendBusRiskScore() {
      this.orgRiskScore.scoreDto = [];
      this.showTree(this.activeArr);
      this.axios
        .post(
          "/risk/org/getContrRiskScore",
          JSON.stringify(this.orgRiskScore),
          {
            headers: { "Content-Type": "application/json" }
          }
        )
        .then(response => {
          if (response.code == "200") {
            this.scoreObject = response.data;
            this.scoreObject.contrScore = response.data[0];
            this.scoreObject.contrLevel = response.data.riskLevel;
            this.firstScore = false;
            this.$forceUpdate();
          } else {
            this.$Message.error(response.message);
          }
        })
        .catch(error => {});
    }
  }
};
</script>
<style lang="scss" scoped>
.CollapseBox .el-collapse-item__content {
  padding-bottom: 0 !important;
}
.tableBox > thead tr {
  min-height: 10px;
}
.tableBox > thead tr td {
  border: none;
}
.textmaxwidth {
  max-width: 200px;
  text-align: center;
}
.part-seconde-step2 .el-collapse-item__header{
  line-height: unset;
  height: auto;
}
.part-seconde-step2 .p1 {
  margin-left: 2%;
  width: 25%;
  float: left;
  text-align: left;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
  white-space: nowrap;
  padding: 0 !important;
  overflow: hidden;
  text-overflow: ellipsis;
}
.part-seconde-step2 .p2 {
  width: 10%;
  float: left;
  text-align: left;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
}
.ppadding {
  padding-left: 20px;
}
.part-seconde-step2 .p3 {
  width: 10%;
  float: left;
  text-align: center;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
}
.part-seconde-step2 .p4 {
  width: 32%;
  float: left;
  text-align: left;
  height: auto;
  box-sizing: border-box;
}
.part-seconde-step2 .p5 {
  width: 10%;
  float: left;
  text-align: center;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
}
.part-seconde-step2 .p6 {
  width: 10%;
  float: left;
  text-align: center;
  height: 40px;
  line-height: 40px;
  box-sizing: border-box;
}
.CollapseBox {
  width: 100%;
  min-height: 100px;
}
.second-step2 {
  margin-bottom: 40px;
  .content {
    .table-info {
      font-size: 14px;
      margin: 20px auto 40px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .target {
      text-align: right;
      margin-bottom: 10px;
      button {
        margin-left: 20px;
        background: #1c67ce;
        color: #fff;
        span {
          color: #fff;
        }
      }
      span {
        font-size: 14px;
        color: #333;
      }
    }
    .tab-desc {
      color: #fff;
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      background: #019fe8;
      padding-left: 20px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .tableBox {
      margin-top: 1px;
    }
    .button-content {
      text-align: center;
      margin: 50px;
      button {
        width: 100px;
        height: 40px;
        font-size: 16px;
        margin: 0 8px;
      }
    }
  }
}
</style>


