<template>
  <div class="first-step">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate"
      :label-width="140"
      class="form-content"
    >
      <FormItem label="评估部门/单位：" prop="orgName" style="width: 400px">
        <Input v-model="formValidate.orgName" placeholder="请输入评估部门/单位" :disabled="disFlg"></Input>
      </FormItem>
      <Row>
        <FormItem label="评估期间：" prop="permissionTime" style="width: 400px" >
          <DatePicker
            :value="formValidate.permissionTime"
            type="daterange"
            placeholder="请选择评估期间"
            @on-change="formValidate.permissionTime=$event"
          ></DatePicker>
        </FormItem>
      </Row>
    </Form>
  </div>
</template>
<script>
export default {
  props: { receiveDatas: Object },
  mounted() {
    console.log("receiveDatas", this.receiveDatas);
    if (this.receiveDatas) {
      this.formValidate.orgName = this.receiveDatas.orgName;
      this.formValidate.permissionTime = [
        this.receiveDatas.evalPeriod,
        this.receiveDatas.evalPeriod1
      ];
    }
  },
  watch: { 
    'formValidate.permissionTime':{
        handler:function(newVal,oldval){
          console.log(newVal)
          this.$emit('changDate',newVal);
        },
        deep:true//对象内部的属性监听，也叫深度监听
    }
  }, 
  data() {
    return {
      disFlg:true,
      formValidate: {
        orgName: "",
        permissionTime: ["", ""]
      },
      ruleValidate: {
        orgName: [
          {
            required: true,
            message: "评估部门/单位不能为空！",
            trigger: "change"
          }
        ],
        permissionTime: {
          type: "array",
          required: true,
          fields: {
            0: {
              required: true,
              message: "评估期间不能为空",
              trigger: "change"
            },
            1: {
              required: true,
              message: "评估期间不能为空",
              trigger: "change"
            }
          }
        }
      }
    };
  },
  methods: {
    handleSubmit(){
      let resObj = {flag:false}

      this.$refs["formValidate"].validate(valid => {
        if (valid) {


            resObj.flag = true;
            this.evalPeriod = this.formValidate.permissionTime[0];
            this.evalPeriod1 = this.formValidate.permissionTime[1];
            let data = {
              id: this.receiveDatas.id,
              evalPeriod: this.formValidate.permissionTime[0],
              evalPeriod1: this.formValidate.permissionTime[1]
            };
            data = JSON.stringify(data);
            this.axios
              .post("/risk/org/getOrgEvalPeriod", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                
              })
              .catch(error => {});
        }else{
          resObj.flag = false;
        }
      });
      return resObj;
    }
  }
};
</script>
<style lang="scss" scoped>
.first-step {
  width: 50%;
  margin: 50px auto 20px;
  .form-content {
    .ivu-date-picker {
      width: 100%;
    }
  }
}
</style>


