<template>
  <div>
    <Upload
      v-show="showFlag"
      ref="upload"
      multiple
      :disabled="fileObj.disabled"
      type="drag"
      :show-upload-list="false"
      :data="uploadParams"
      :default-file-list="defaultList"
      :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
      :max-size="10240"
      :on-format-error="handleFormatError"
      :on-exceeded-size="handleMaxSize"
      :before-upload="handleUploadfile"
      :action="axios.defaults.baseURL+'/system/remote/micUpload'"
      :on-success="handleSuccess"
      :on-error="handleError"
    >
      <div style="padding: 20px 0" v-show="showFlag">
        <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
        <p>请上传附件</p>
      </div>
    </Upload>
    <div v-show="showFlag">上传格式为zip、rar、xlsx、docx、pdf，且大小不超过10MB</div>
    <Table border :columns="columns1" :data="uploadList"></Table>
  </div>
</template>
<script>
export default {
  props: ["fileObj"],
  data() {
    return {
      showFlag: true, //是否显示上传文件 true:显示上传 false：隐藏上传
      defaultList: [],
      uploadList: [],
      uploadParams: {},
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small",
                    disabled: this.fileObj.disabled
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index, params.row.name);
                    }
                  }
                },
                "下载"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small",
                    disabled: this.fileObj.disabled
                  },
                  style: {
                    display: this.showFlag ? "" : "none"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  created() {
    // 判断是否busid传过来
    if (Object.keys(this.fileObj).length > 0) {
      let obj = this.fileObj;
      let busId = obj.data.busId;
      let fileList = obj.fileList;
      this.showFlag = obj.showFlag;
      if (busId) {
        this.getFileLists(busId);
      }else if(fileList && fileList.length > 0){
        this.getFileListsByfileList(fileList);
      }
    }
  },
  methods: {
    getFileListsByfileList(fileList){
      this.defaultList = fileList;
      this.uploadList = fileList;
    },
    getFileLists(busId) {
      this.axios
        .post(`/system/remote/getFileList/${busId}`)
        .then(res => {
          if (res.code == "200") {
            let fileList = res.data;
            for (let i = 0; i < fileList.length; i++) {
              this.defaultList.push({
                url: fileList[i].fileId,
                name: fileList[i].filename
              });
            }
            this.$nextTick(() => {
              if (this.$refs.upload) {
                this.uploadList = this.$refs.upload.fileList;
              }
            });
          }
        })
        .catch(function(error) {});
    },
    handleUploadfile(file) {
      const check = this.uploadList.length < 5;
      if (!check) {
        this.$Notice.warning({
          title: "只能上传最多五个附件."
        });
      } else {
        this.$Spin.show({
          render: h => {
            return h("div", [
              h("Icon", {
                class: "demo-spin-icon-load",
                props: {
                  type: "ios-loading",
                  size: 18
                }
              }),
              h("div", "上传中...")
            ]);
          }
        });
      }
      return check;
    },
    handleSuccess(response, file, fileList) {
      if (response.code == "200") {
        file.url = response.data[0].fileId;
        this.uploadList = fileList;
        this.$emit("requestUploadList", this.uploadList);
        this.$Spin.hide();
      }
    },
    handleError() {
      this.$Spin.hide();
      this.$Message.error("上传失败");
    },
    handleFormatError(file) {
      this.$Spin.hide();
      this.$Notice.warning({
        title: "附件格式错误",
        desc: "请选择zip、rar、xls、xlsx、doc、docx、pdf."
      });
    },
    handleMaxSize(file) {
      this.$Spin.hide();
      this.$Notice.warning({
        title: "超出了附件大小限制",
        desc: " 不应超过10M"
      });
    },
    remove(index) {
      let filePath = this.uploadList[index].url;
      if (this.fileObj.worfFlow) {
        this.uploadList.splice(index, 1);
        this.$emit("requestUploadList", this.uploadList);
        this.$Message.success("删除成功");
      } else {
        this.axios.post(`/system/remote/delFile/${filePath}`).then(response => {
          if ((response = true)) {
            this.uploadList.splice(index, 1);
            this.$emit("requestUploadList", this.uploadList);
            this.$Message.success("删除成功");
          } else {
            this.$Message.error("删除失败");
          }
        });
      }
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    }
  }
};
</script>


