<template>
  <div class="third-step">
    <div class="content">
      <div class="table-info">
        <span>评估部门/单位：{{result.orgName}}</span>
        <span>评估期间：{{result.evalPeriod}}~{{result.evalPeriod1}}</span>
      </div>
      <div class="tab-desc">
        <label>评估结果</label>
      </div>
      <div class="content-detail">
        <div class="title-content">
          <div>
            固有风险评估：
            <span>{{result.evalScore}}，{{result.evalLevel}}</span>
          </div>
          <div>
            控制措施有效性评估：
            <span>{{result.contrScore}}，{{result.contrLevel}}</span>
          </div>
          <div>
            综合评估：
            <span>{{result.compLevel}}，{{result.compName}}</span>
          </div>
        </div>
        <table class="first-tab" cellspacing="0" cellpadding="0">
          <tr v-for="(tabledata,index) in tabledatas" :key="index">
            <td
              v-for="(tddata,tdIndex) in tabledata"
              :style="{'background':tddata.color}"
              :class="(tddata.levelCodeName1==positons[0]) && (tddata.levelCodeName2 ==positons[1])?'td-scale':'' "
            >
              <template v-if="index < tabledatas.length-1">
                <template v-if="tdIndex==0">{{tddata.levelCode}}</template>
                <template v-else>{{tddata.levelCode}}</template>
              </template>
              <template v-else>
                <template v-if="tdIndex==0">
                  <template v-for="(item,index1) in tddata.levelCode">
                    <div>{{item}}</div>
                    <p v-if="index1 < 1"></p>
                  </template>
                </template>
                <template v-else>{{tddata.levelCode}}</template>
              </template>
            </td>
          </tr>
        </table>
      </div>
      <div class="form">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="120">
          <FormItem label="流程标题" prop="taskName">
            <Input
              v-model="formValidate.taskName"
              type="textarea"
              :autosize="{minRows: 1,maxRows: 1}"
              placeholder="请输入流程标题！"
            ></Input>
          </FormItem>

          <FormItem label="评估备注" prop="remark">
            <Input
              v-model="formValidate.remark"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入评估备注！"
            ></Input>
          </FormItem>
          <Row>
            <FormItem label="评估附件" prop="xxxx">
              <showUsualFiles
                :file-obj="fileObj"
                :key="fileId"
                @requestUploadList="receiveUploadList"
              ></showUsualFiles>
            </FormItem>
          </Row>
          <Modal :mask-closable="false" v-model="modal1" width="360" class="treeBoxModel">
            <p slot="header" style="color:#f60;text-align:center">
              <span>审核人信息</span>
            </p>
            <div style="text-align:left;overflow-y:auto;height:300px">
              <FormItem prop="nextUser">
                <Tree
                  ref="treeRef"
                  :data="data1"
                  show-checkbox
                  multiple
                  @on-check-change="updateNextUser"
                ></Tree>
              </FormItem>
            </div>
            <div slot="footer">
              <Button type="primary" size="large" long @click="submit()">提交审核</Button>
            </div>
          </Modal>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";

export default {
  props: {
    receiveDatas: Object
  },
  components: { showUsualFiles },
  created() {
    let that = this;
    this.axios
      .post(`/risk/org/orgResult?id=${this.receiveDatas.id}`)
      .then(response => {
        if (response.code == "200") {
          let data = response.data;
          this.result = response.data;
          this.positons[0] = this.result.evalLevel;
          this.positons[1] = this.result.contrLevel;
          this.formValidate = {
            remark: data.remark,
            taskName: data.taskName
          }
          let fileList = [];
          if(data.fileName){
            let fileName = JSON.parse(data.fileName);
            let filePath = JSON.parse(data.filePath);
            for (let i = 0; i < fileName.length; i++) {
              fileList.push({
                url: filePath[i],
                name: fileName[i]
              });
            }
          }
          this.fileObj = {
            data: data,
            showFlag: true,
            fileList: fileList
          };
          this.fileId = data.id;
        }
      })
      .catch(error => {});

    this.axios
      .post("/risk/org/getOrgCompLevelPhone")
      .then(response => {
        if (response.code == "200") {
          if (response.code == "200") {
            let data = response.data;
            let forwCode = data[0].levelCode1;
            let arraTemp = [];
            let lastArraTemp = [];
            lastArraTemp.push({
              levelCode: ["固有风险", "控制措施有效性"],
              color: ""
            });
            arraTemp.push({
              levelCode: data[0].levelCodeName1,
              color: ""
            });
            let flag = true;
            for (let i = 0; i < data.length; i++) {
              let index = 1;
              if (data[i].levelCode1 == forwCode) {
                arraTemp.push(data[i]);
                if (flag)
                  lastArraTemp.push({
                    levelCode: data[i].levelCodeName2,
                    color: ""
                  });
              } else {
                forwCode = data[i].levelCode1;
                flag = false;
                this.tabledatas.push(JSON.parse(JSON.stringify(arraTemp)));
                arraTemp = [];
                arraTemp.push({
                  levelCode: data[i].levelCodeName1,
                  color: ""
                });
                arraTemp.push(data[i]);
              }
            }
            this.tabledatas.push(JSON.parse(JSON.stringify(arraTemp)));
            this.tabledatas.push(lastArraTemp);
          }
        }
      })
      .catch(error => {});
    let url = `/bpm/getNodeDefDetail?bpmType=org_risk`;
    this.axios
      .get(url)
      .then(response => {
        let data = {
          bpmName: response.data[1].bpmName,
          nodeId: response.data[1].nodeId,
          roleName: response.data[1].roleName
        };
        this.orgResult.bpmName = data.bpmName;
        data = JSON.stringify(data);
        this.axios
          .post("/bpm/getNextRole", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(res => {
            if (res.code == "200") {
              this.data1 = res.data;
              this.treeNLeafNodeDisable(this.data1);
            }
          })
          .catch(error => {});
      })
      .catch(function(error) {});
  },
  data() {
    return {
      data1: [],
      fileId: "",
      fileObj: {},
      orgResult: {
        nextUser: "",
        filePath: "",
        fileName: "",
        bpmName: "",
        orgId: "",
        remark: "",
        taskName: ""
      },
      modal1: false,
      businessId: "", //参数
      result: {},
      uploadList: [],
      tabledatas: [],
      positons: ["较低", "中"],
      columns1: [
        {
          title: "文件名",
          key: "name"
        },
        {
          title: "操作",
          key: "",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.fileDownload(params.index, params.row.name);
                    }
                  }
                },
                "下载"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ],
      title: {},
      formValidate: {
        remark: "",
        taskName: ""
      },
      ruleValidate: {
        remark: [
          {
            required: true,
            message: "评估备注不能为空",
            trigger: "blur"
          },
          {
            required:false,
            message: '评估备注不能超过1000字',
            trigger: 'blur',
            max:1000
          },
        ],
        taskName: [
          {
            required: true,
            message: "流程标题不能为空",
            trigger: "blur"
          },
          {
            type: "string",
            max: 255,
            message: "流程标题不能超过255个字",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    receiveUploadList(list) {
      this.uploadList = {
        fileName: [],
        filePath: []
      };
      list.forEach(item => {
        this.uploadList.filePath.push(item.url);
        this.uploadList.fileName.push(item.name);
      });
      console.log("receiveUploadlist", this.uploadList);
    },
    next() {
      this.modal1 = true;
    },
    submit() {
      this.$refs['formValidate'].validate(valid => {
        if (valid) {
          this.orgResult.fileName = JSON.stringify(this.uploadList.fileName);
          this.orgResult.filePath = JSON.stringify(this.uploadList.filePath);
          let data = JSON.parse(JSON.stringify(this.orgResult));
          data.remark = this.formValidate.remark;
          data.taskName = this.formValidate.taskName;
          data.orgId = this.receiveDatas.id;
          data = JSON.stringify(data);
          this.axios
            .post("/risk/org/orgTask", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("提交成功");
                this.modal1 = false;
                this.$emit("cancelDialog",false);
              } else {
                this.$Message.error("提交失败");
                this.handleSubmitDisable = false;
              }
            });
        }
      })
      
    },
    treeNLeafNodeDisable(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].children) {
          this.$set(treeDatas[i], "disabled", true);
          this.$set(treeDatas[i], "expand", true);
          this.treeNLeafNodeDisable(treeDatas[i].children);
        }
      }
    },
    updateNextUser(nodes, node) {
      nodes.forEach(el => {
        if (el.nodeKey != node.nodeKey) {
          el.checked = false;
        }
      });
      this.orgResult.nextUser = "";
      nodes.forEach(element => {
        if (element.checked == true) this.orgResult.nextUser = element.code;
      });
      this.$refs["formValidate"].validateField("nextUser");
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    }
  }
};
</script>
<style lang="scss" scoped>
.third-step {
  .content {
    width: 1000px;
    .table-info {
      font-size: 14px;
      margin: 20px auto 40px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .tab-desc {
      color: #fff;
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      background: #019fe8;
      padding-left: 20px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .content-detail {
      background: #fff;
      padding: 50px 100px;
      font-size: 16px;
      border: 1px solid #d9d9d9;
      .first-tab {
        width: 100%;
        border: 1px solid #797979;
        margin-top: 15px;
        td,
        th {
          border: 0.5px solid #797979;
          height: 40px;
          line-height: 40px;
          text-align: center;
          width: 100px;
          position: relative;
          p {
            width: 159px;
            height: 1px;
            background: #797979;
            transform: rotate(146deg);
            position: absolute;
            left: -13px;
            top: 44px;
          }
          div {
            &:first-child {
              position: relative;
              top: -1px;
              left: -20px;
            }
            &:nth-child(3) {
              width: 70px;
              position: relative;
              right: -60px;
              line-height: 20px;
              margin: 5px 0;
              text-align: left;
            }
          }
          &.td-scale {
            transform: scale(1.1);
            z-index: 2;
            font-size: 22px;
            font-weight: bold;
            box-shadow: 5px 5px 5px;
          }
        }
      }
      .title-content {
        line-height: 40px;
      }
    }
    .form {
      margin-top: 25px;
    }
  }
}
</style>


