<template>
    <div class="part-evalue">
        <div class="topTitle">
            <span class="topTitle-span1">洗钱风险评估</span>
            <span class="topTitle-span1">
                <img src="../../../../assets/topTitle1.png" alt />
            </span>
            <span class="topTitle-span1">机构洗钱风险评估（评估基础信息）</span>
        </div>
        <div class="center">
            <div class="step-content">
                <Steps :current="current">
                    <Step :title="title" v-for="(title,index) in stepList" :key="index"></Step>
                </Steps>
            </div>
            <div>
                <firstStep ref="firstStep" :receiveDatas="receiveDatas" v-if="firFlg" @changDate="changeDate"></firstStep>
                <partSecondStep ref="partSecondStep" :receiveDatas="receiveDatas" v-if="secFlg"> </partSecondStep>
                <partSecondStep2 ref="partSecondStep2"  :receiveDatas="receiveDatas" v-if="thirdFlg"></partSecondStep2>
                <thirdStep ref="thirdStep" @cancelDialog="cancelDialog" :receiveDatas="receiveDatas" v-if="fourFlg"></thirdStep>
            </div>
            <div :class="current==3?'button-content1 mR_60':'button-content1'" >
                <Button type="primary" @click="prev" v-if="current > 0">上一步</Button>
                <Button type="primary" @click="handleSave" v-if="current ==1 || current==2">保存</Button>
                <Button type="primary" @click="next" v-if="current != 3" >下一步</Button>
                <Button type="primary" @click="submit" v-if="current == 3">提交审核</Button>
            </div>
        </div>
    </div>
</template>
<script>
import firstStep from './firstStep'
import partSecondStep from './partSecondStep'
import partSecondStep2 from './partSecondStep2'
import thirdStep from './thirdStep'
export default {
    props: { dataObj: Object },
    created(){
        if(this.$route.query.current == 2){
            this.current = 2;
            this.switchComp();
        }else if(this.$route.query.current == 1){
            this.current = 1;
            this.switchComp();
        }
        this.receiveDatas = this.dataObj;
    },
    data(){
        return{
            firFlg:true,
            secFlg:false,
            thirdFlg:false,
            fourFlg:false,
            receiveDatas:{
               data:""
            },
            current:0,//0 评估基础信息 1.固有风险 2控制措施有效 3 评估结果
            stepList:['评估基础信息','固有风险评估','控制措施有效性评估','评估结果'],
            tabview:'firstStep',
        }
    },
    components:{
        firstStep,
        partSecondStep,
        partSecondStep2,
        thirdStep,
    },
    methods:{
        changeDate(date){
            this.dataObj.evalPeriod = date[0];
            this.dataObj.evalPeriod1 = date[1];
            console.log('this.dataObj', this.dataObj);
        },
        submit(){
            this.$refs.thirdStep.next();
        },
        handleSave(){
            if(this.current==1){
                this.$refs.partSecondStep.handleSave();
            }
            if(this.current==2){
                 this.$refs.partSecondStep2.handleSave();
            }

        },
        prev(){
            if(this.current > 0){
                this.current --;
            }
            this.switchComp();
        },
        next () {
            if (this.current == 3) {
                this.current = 0;
            } else {
                this.current += 1;
            }
            if(this.current == 1){
                let flagObj = this.$refs.firstStep.handleSubmit();
                if(!flagObj.flag){
                    this.current = 0;
                    return false;
                }
                this.switchComp();
            }
            //固有风险评估下一步保存
            if(this.current == 2){
                this.$refs.partSecondStep.handleSave("nextStep");
                this.switchComp();

            }
            //控制措施有效性评估保存
            if(this.current == 3){
                debugger
                console.log('current',3);
                this.saveContrl();
            }
            
        },
        //控制措施有效性评估保存
        async saveContrl(){
                await this.$refs.partSecondStep2.handleSave("nextStep");
                this.switchComp();
        },
        cancelDialog(val){
            this.$emit("cancelModal", val);
        },
        switchComp(){
            console.log('switchComp');
            switch(this.current){
                case 0 :
                    // this.tabview = 'firstStep';
                    this.firFlg = true;
                    this.thirdFlg = false;
                    this.fourFlg = false;
                    this.secFlg=false;
                    break;
                case 1 :
                    // this.tabview = 'partSecondStep';
                    this.firFlg = false;
                    this.thirdFlg = false;
                    this.fourFlg = false;
                    this.secFlg=true;
                    break;
                case 2 :
                    // this.tabview = 'partSecondStep2';
                    this.firFlg = false;
                    this.thirdFlg = true;
                    this.fourFlg = false;
                    this.secFlg=false;
                    break;
                case 3 :
                    // this.tabview = 'thirdStep';
                    this.firFlg = false;
                    this.thirdFlg = false;
                    this.fourFlg = true;
                    this.secFlg=false;
                    break;
            }
        }
    }
}
</script>
<style lang="scss" @scoped>
    .part-evalue{
        .center{
            background: #fff;
            padding: 20px 20px 40px;
            margin-top: 10px;
           .step-content{
               .ivu-steps{
                   line-height: 3;
                   .ivu-steps-head{
                       .ivu-steps-icon.ivu-icon{
                           font-size: 40px;
                       }
                       .ivu-steps-head-inner{
                            width: 40px;
                            height: 40px;
                            line-height: 40px;
                            font-size: 18px;
                       }
                   }
               }
           } 
           .button-content1{
               text-align: center;
               margin: 0;
               width: 100%;
               button{
                width: 100px;
                height: 40px;
                font-size: 16px;
                margin: 0 8px;
               }
           }
           .width_1000{
               width: 1000px;
           }
           .mR_60{
               margin-right: 60px;
           }
        }
    }
</style>


