<template>
    <div class="view-count">
        <table cellspacing="0" cellpadding="0" class="view-table">
            <thead>
                <th>浏览用户</th>
                <th>浏览时间</th>
                <th>所属部门</th>
            </thead>
            <tbody>
                <tr v-for="(item, index) in tableBodys" :key="index">
                    <td>{{item.userName}}</td>
                    <td>{{item.viewTime}}</td>
                    <td>{{item.dept}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    props:['dataObj'],
    data(){
        return{
            tableBodys:[]
        }
    },
    created(){
        let data = this.dataObj.data;
        if(data && data.busId){
            this.getInfViewNumDetail(data.busId);
        }
    },
    methods:{
        getInfViewNumDetail(busId){
            this.axios
            .post("/process/getInfViewNumDetail/"+busId, {
                headers: { "Content-Type": "application/json" }
                })
            .then(response => {
                if (response.code == "200") {
                    this.tableBodys = response.data;
                }
            })
            .catch(error => {});
            }
        

    }
}
</script>


