<template>
  <div>
    <div class="contentCellBox">
      <div class="contentCellBody flexStart">
        <div class="taskAuditDataExtraction-left" style="width:100%">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>流程信息
          </div>
          <Row>
            <table class="tableBox" style="margin-top:0">
              <thead>
                <tr>
                  <td>流程名称</td>
                  <td colspan="8">{{addNm}}{{processTable.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>流程步骤</td>
                  <td>发起时间</td>
                  <td>结束时间</td>
                  <td>办理人</td>
                  <td>办理人部门</td>
                  <td>结果</td>
                  <td>意见</td>
                  <td>下一步骤</td>
                  <td>下一处理人</td>
                </tr>
                <tr v-for="(item ,index) in processTable.content">
                  <td>{{item.actionName}}</td>
                  <td>{{item.startTime}}</td>
                  <td>{{item.endTime}}</td>
                  <td>{{item.userName}}</td>
                  <td>{{item.userDeptName}}</td>
                  <td>{{item.approveResult==0||item.approveResult=='null'?'审核通过':'审核退回'}}</td>
                  <td
                    class="opinions"
                    :title="item.approveNote=='null'?'通过':item.approveNote"
                  >{{item.approveNote=='null'?'通过':item.approveNote}}</td>
                  <td>{{item.nextActivityName}}</td>
                  <td>{{item.nextUserName}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:['dataObj'],
  data() {
    return {
      addNm:'',
      processTable: {
        name: "审核流程",
        content: [
        ]
      }
    };
  },
  created(){
    this.addNm = this.dataObj.name;
    if(this.dataObj.documentId){
      let documentId = this.dataObj.documentId;
      this.getDatasBydocId(JSON.stringify({
        documentId: documentId
      }));
    }else if(this.dataObj.id){
      this.getDatasById(this.dataObj.id)
    }else if(this.dataObj.businessId){
      this.getDatasByBusinessId(this.dataObj.businessId)
    }
  },
  methods: {
    // param:documentId
    getDatasBydocId(documentIdObj){
      this.axios
      .post("/bpm/open", documentIdObj, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == '200') {
          if (response.data.httpCode == "200")
              this.processTable.content = response.data.dataVo.records.comments;
          else this.$Message.error(response.data.msg)
        }else{
          this.$Message.error(response.message)
        }
      })
      .catch(error => {console.log('error',error)});
    },
    getDatasById(id) {
      this.axios
        .post("/risk/pro/getProRiskBpm?id="+id)
        .then(response => {
          if (response.code == "200"){
            this.processTable.content = response.data.dataVo.records.comments;
          } 
        })
        .catch(error => {
          console.log('error', error)
        });
    },
    // param:businessId(业务businessId)
    getDatasByBusinessId(businessId) {
      this.axios
        .post("/bpm/getBpm/"+businessId, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200"){
            this.processTable.content = response.data.dataVo.records.comments;
          } 
        })
        .catch(error => {
          console.log('error', error)
        });
    },
  }
};
</script>
<style scoped>
.fileBox {
  padding: 20px 0;
}
.fileBox > p {
  width: 200px;
  text-align: left;
  height: 20px;
  line-height: 16px;
  border: 1px solid RGB(241, 243, 244);
  margin: 10px;
  padding: 2px;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
}
.fileBox > p:hover {
  background-color: RGB(45, 140, 240);
  color: white;
}
</style>


