<template>
    <div class="tab2-analyze">
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <template v-for="(item,index) in tableBody">
                  <tr>
                    <td>{{index+1}}</td>
                    <td>{{item.code}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.roleDescribe}}</td>
                    <td>{{item.roleDescribe}}</td>
                    <td>{{item.roleDescribe}}</td>
                    <td>{{item.Remarks}}</td>
                    <td>{{item.Remarks}}</td>
                  </tr>
                </template>
              </tbody>
            </table>
          </Row>
          <div class="flexCenter pageBox">
            <Page :total="100" show-elevator show-sizer></Page>
          </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            tableHeader: [
                { name: "序" },
                { name: "因子项目" },
                { name: "对应分值" },
                { name: "低风险机构数" },
                { name: "较低风险机构数" },
                { name: "中风险机构数" },
                { name: "较高风险机构数" },
                { name: "高风险机构数" },
            ],

            tableBody: [
                {
                id: "12",
                code: "111",
                name: "低",
                roleDescribe: "0",
                Remarks: "50"
                }
            ]
        }
    }
}
</script>
<style scoped>

</style>


