<template>
  <div class="assess-risk">
    <div class="content">
      <div class="contentCellTitle ">
          <div class="contentCellTitle-1"></div>产品信息
      </div>
      <div class="tab-desc">
        <span>创建日期：</span>{{proInfo.createTime?proInfo.createTime.substr(0,10):''}}
        <span>创建人员：</span>{{proInfo.creator}}
      </div>
      <table class="first-tab" cellpadding="0" style="border-collapse:separate; border-spacing:0px 0px 1px">
        <tr>
          <td>业务类型</td>
          <td>{{proInfo.busType}}</td>
          <td>业务名称</td>
          <td class="textOverflow textmaxwidth" :title="proInfo.busName">{{proInfo.busName}}</td>
        </tr>
        <tr>
          <td>产品代码</td>
          <td>{{proInfo.proCode}}</td>
          <td>产品类型</td>
          <td>{{proInfo.proType}}</td>
        </tr>
        <tr>
          <td>产品名称</td>
          <td class="textOverflow textmaxwidth" :title="proInfo.proName">{{proInfo.proName}}</td>
          <td>所属业务部门</td>
          <td>{{proInfo.department}} </td>
        </tr>
        <tr>
          <td>成立日期</td>
          <td>{{proInfo.buildDate?proInfo.buildDate.substr(0,10):''}}</td>
          <td>期限</td>
          <td>{{proInfo.term}}</td>
        </tr>
        <tr>
          <td>是否允许境外客户</td>
          <td>{{proInfo.abroad}}</td>
          <td>是否业务投资境外</td>
          <td>{{proInfo.investmentMark}}</td>
        </tr>
        <tr>
          <td>产品描述</td>
          <td class="textOverflow textmaxwidth" :title="proInfo.proDescribe">{{proInfo.proDescribe}}</td>
        </tr>
      </table>
      <div class="contentCellTitle ">
          <div class="contentCellTitle-1" ref="target"></div>评估信息
      </div>
      <div class="collapse-content">
        <div class="content1">
            <span>{{indicatorName}}</span>
            <span v-tdloss="{code:proRiskScore.sysLevel,list:riskLevels}"></span>
            <span>总分：{{scoreObject['0']}}分</span>
            <span>
                <Poptip placement="top" width="400" trigger="hover">
                    <Icon type="ios-information" />
                    <div class="api" slot="content">
                        {{message}}
                    </div>
                </Poptip>
            </span>
        </div>
        <table class="tableBox third-tab" >
          <thead :class="positonFix?' positon-fix pos-fix-thead':''">
              <tr >
                  <td v-for="item in thead">{{item.name}}</td>
              </tr>
          </thead>
        </table>
            <Collapse  v-model="panelA" simple>
              <template v-for="item in indicator">
                <Panel :name="item.indicatorCode" :key="item.indicatorCode" :hide-arrow="item.children == undefined">
                    <template v-if="item.children != undefined">
                        <span class="buss-title">{{item.indicatorCode}}{{item.indicatorName+'('}}{{item.riskBonus == '1'?'风险加分项)':item.weight+'%)'}}</span>
                        <span clss="totalPercent">总分</span>{{firstScore?item.defaultValue:scoreObject[item.indicatorCode]}}
                        <template v-for="ii in item.children">
                            <p slot="content" v-if="ii.children == undefined" :key="ii.indicatorCode">
                                <table class="tableBox third-tab" >
                                    <tbody>
                                        <tr>
                                            <td>{{ii.indicatorCode}}</td>
                                            <td>{{ii.indicatorName+'('}}{{ii.riskBonus == '1'?'风险加分项)':ii.weight+'%)'}}</td>
                                            <td>
                                                    <Radio
                                                      v-for="(item3, index3) in ii.proEvalOptions"
                                                      :value="item3.checked"
                                                      @on-change="monopo(ii.proEvalOptions, index3, ii.indicatorCode)"
                                                      :label="item3.optId"
                                                      :key="item3.optId"
                                                    >{{item3.enums+'('+item3.score+')'}}</Radio>
                                            </td>
                                            <td>{{firstScore?ii.defaultValue:scoreObject[ii.indicatorCode]}}</td>
                                            <td>{{ii.indicatorType}}</td>
                                            <td>
                                                <Input v-model="ii.trueData" @on-change="updatetrueData(ii.indicatorCode, ii.trueData)"></Input>
                                            </td>
                                            <td>
                                              <Input v-model="ii.detailRemark" @on-change="updatedetailRemark(ii.indicatorCode, ii.detailRemark)"></Input>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </p>
                            <div slot="content" v-if="ii.children != undefined" :key="ii.indicatorCode">
                                <Collapse v-model="panelB" simple>
                                    <Panel :name="ii.indicatorCode" :key="ii.indicatorCode">
                                        <span class="buss-title">{{ii.indicatorCode}}{{ii.indicatorName+'('}}{{ii.riskBonus == '1'?'风险加分项)':ii.weight+'%)'}}</span> 
                                        <span clss="totalPercent">总分</span>{{firstScore?ii.defaultValue:scoreObject[ii.indicatorCode]}}
                                        <template v-for="iii in ii.children">
                                            <p slot="content" v-if="iii.children == undefined" :key="iii.indicatorCode">
                                                <table class="tableBox third-tab" >
                                                    <tbody>
                                                        <tr>
                                                            <td>{{iii.indicatorCode}}</td>
                                                            <td>{{iii.indicatorName+'('}}{{iii.riskBonus == '1'?'风险加分项)':iii.weight+'%)'}}</td>
                                                            <td>
                                                                <Radio
                                                                  v-for="(item4, index4) in iii.proEvalOptions"
                                                                  :value="item4.checked"
                                                                  @on-change="monopo(iii.proEvalOptions, index4, iii.indicatorCode)"
                                                                  :label="item4.optId"
                                                                  :key="item4.optId"
                                                                >{{item4.enums+'('+item4.score+')'}}</Radio>
                                                            </td>
                                                            <td>{{firstScore?iii.defaultValue:scoreObject[iii.indicatorCode]}}</td>
                                                            <td>{{iii.indicatorType}}</td>
                                                            <td>
                                                                <Input v-model="iii.trueData" @on-change="updatetrueData(iii.indicatorCode, iii.trueData)"></Input>
                                                            </td>
                                                            <td>
                                                              <Input v-model="iii.detailRemark" @on-change="updatedetailRemark(iii.indicatorCode, iii.detailRemark)"></Input>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </p>
                                            <div slot="content" v-if="iii.children != undefined" :key="iii.indicatorCode">
                                                <Collapse v-model="panelC" simple>
                                                    <Panel :name="iii.indicatorCode" :key="iii.indicatorCode">
                                                        <span class="buss-title">{{iii.indicatorCode}}{{iii.indicatorName+'('}}{{iii.riskBonus == '1'?'风险加分项)':iii.weight+'%)'}}</span> 
                                                        <span clss="totalPercent">总分</span>{{firstScore?iii.defaultValue:scoreObject[iii.indicatorCode]}}
                                                        <p slot="content">
                                                            <table class="tableBox third-tab" >
                                                                <tbody>
                                                                    <tr v-for="item4 in iii.children" :key="item4.indicatorCode">
                                                                        <td>{{item4.indicatorCode}}</td>
                                                                        <td>{{item4.indicatorName+'('}}{{item4.riskBonus == '1'?'风险加分项)':item4.weight+'%)'}}</td>
                                                                        <td>
                                                                          <Radio
                                                                            v-for="(item5, index5) in item4.proEvalOptions"
                                                                            :value="item5.checked"
                                                                            @on-change="monopo(item4.proEvalOptions, index5, item4.indicatorCode)"
                                                                            :label="item5.optId"
                                                                            :key="item5.optId"
                                                                          >{{item5.enums+'('+item5.score+')'}}</Radio>
                                                                        </td>
                                                                        <td>{{firstScore?item4.defaultValue:scoreObject[item4.indicatorCode]}}</td>
                                                                        <td>{{item4.indicatorType}}</td>
                                                                        <td>
                                                                            <Input v-model="item4.trueData" @on-change="updatetrueData(item4.indicatorCode, item4.trueData)"></Input>
                                                                        </td>
                                                                        <td>
                                                                          <Input v-model="item4.detailRemark" @on-change="updatedetailRemark(item4.indicatorCode, item4.detailRemark)"></Input>
                                                                        </td>
                                                                    </tr>
                                                                </tbody>
                                                            </table>
                                                        </p>
                                                  </Panel>
                                                </Collapse>
                                            </div>
                                        </template>
                                  </Panel>
                                </Collapse>
                            </div>
                        </template>
                    </template>
                    <template v-else>
                      <p slot="content">
                          <table class="tableBox third-tab" >
                                <tbody>
                                    <tr>
                                        <td>{{item.indicatorCode}}</td>
                                        <td>{{item.indicatorName+'('}}{{item.riskBonus == '1'?'风险加分项)':item.weight+'%)'}}</td>
                                        <td>
                                                <Radio
                                                  v-for="(item2, index2) in item.proEvalOptions"
                                                  :value="item2.checked"
                                                  @on-change="monopo(item.proEvalOptions, index2, item.indicatorCode)"
                                                  :label="item2.optId"
                                                  :key="item2.optId"
                                                >{{item2.enums+'('+item2.score+')'}}</Radio>
                                        </td>
                                        <td>{{firstScore?item.defaultValue:scoreObject[item.indicatorCode]}}</td>
                                        <td>{{item.indicatorType}}</td>
                                        <td>
                                            <Input v-model="item.trueData" @on-change="updatetrueData(item.indicatorCode, item.trueData)"></Input>
                                        </td>
                                        <td>
                                          <Input v-model="item.detailRemark" @on-change="updatedetailRemark(item.indicatorCode, item.detailRemark)"></Input>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </p>
                    </template>
                </Panel>
              </template>
            </Collapse>
    </div>
      <div class="form-content" ref="history">
        <Form ref="formValidate" :model="proRiskScore" :rules="ruleValidate" :label-width="120">
          <div class="container">
            <label class="special-label"><Icon type="ios-information-circle-outline" />评估完成需人工确认</label>
            <Row>
              <Col span="11">
                <FormItem label="流程标题" prop="taskName">
                    <Input v-model="proRiskScore.taskName" placeholder="请输入流程标题"></Input>
                </FormItem>
              </Col>
            </Row>
            <FormItem label="系统评级等级">
                <label  v-tdloss="{code:proRiskScore.sysLevel,list:riskLevels}"></label>
            </FormItem>
            <Row>
              <Col span="11">
                <FormItem label="确认等级" prop="confirmLevel">
                    <Select v-model="proRiskScore.confirmLevel" placeholder="请输入确认等级">
                      <Option v-for="item in riskLevels" :value="item.levelCode" :key="item.levelCode">{{item.levelName}}</Option>
                    </Select>
                </FormItem>
              </Col>
              <span class="speical-span">
                <FormItem></FormItem>
              </span>
              <Col span="11">
                <FormItem  label="下次评估日期">
                    <DatePicker type="date" placeholder="请选择下次评估日期" :value="proRiskScore.nextEvalDate" @on-change="proRiskScore.nextEvalDate=$event" :options="options1"></DatePicker>
                </FormItem>
              </Col>
            </Row>
            <FormItem label="评估备注" prop='remark' :rules="remarkMust == true?ruleValidate.remark:ruleValidate.remark2">
                <Input
                v-model="proRiskScore.remark"
                type="textarea"
                :autosize="{minRows: 2,maxRows: 5}"
                placeholder="请输入评估备注"
                ></Input>
            </FormItem>
            <Row>
              <Form-item label="附件：" class="form-item" style="width:100%">
                 <Upload
                  ref="upload"
                  multiple
                  type="drag"
                  :show-upload-list="false"
                  :default-file-list="defaultList"
                  :before-upload="handleUploadfile"
                  :format="['zip','rar','xls','xlsx','doc','docx','pdf']"
                  :max-size="10240"
                  :on-format-error="handleFormatError"
                  :on-exceeded-size="handleMaxSize"
                  :action="axios.defaults.baseURL+'/system/remote/micUpload'"
                  :on-success="handleSuccess"
                  :on-error="handleError"
                >
                  <div style="padding: 20px 0">
                    <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                    <p>请上传附件</p>
                  </div>
                </Upload>
                <div>上传格式为zip、rar、xls、xlsx、doc、docx、pdf，且大小不超过10MB</div>
              </Form-item>
              <Form-item label=""  class="form-item" style="width:100%">
                 <Table border :columns="columns1" :data="uploadList"></Table>
              </Form-item>
            </Row>
          </div>
            <FormItem>
              <div class="button-content">
                <Button type="primary" @click="handleSave('formValidate')" :disabled='handleSaveDisable'>保存</Button>
                <Button type="primary" class="blue" @click="next('formValidate')" :disabled="handleSubmitDisable">下一步</Button>
                <Button @click="handleCancel">取消</Button>
              </div>
            </FormItem>
            <Modal :mask-closable="false"   v-model="modal1" width="360" class="treeBoxModel">
              <p slot="header" style="color:#f60;text-align:center">
                <span>审核人信息</span>
              </p>
              <div style="text-align:left;overflow-y:auto;height:300px">
                <FormItem prop="nextUser">
                  <Tree
                    ref="treeRef"
                    :data="data1"
                    show-checkbox
                    multiple
                    @on-check-change="updateNextUser"
                  ></Tree>
                </FormItem>
              </div>
              <div slot="footer">
                <Button type="primary" size="large" long @click="handleSubmit('formValidate')"  :disabled="handleSubmitDisable">提交审核</Button>
              </div>
            </Modal>
        </Form>
        <Modal :mask-closable="false"   v-model="modal2" title="错误列表">
             <div class="fujian">
              <div v-for="(item,index) in errorList" :key="index">
                <span
                  style="width:100%;height:40px;line-height:20px;overflow:hidden;text-overflow: ellipsis;margin-bottom:5px"
                  class="cursorPointer"
                >{{item.message}}</span>
              </div>
            </div>
            <div slot="footer">
            </div>
      </Modal>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props:{dataObj: Object},
  created() {
    let that = this;
    if (this.dataObj.id) {
      this.axios
        .post("/risk/pro/getProRiskEvalInfo", JSON.stringify({id: this.dataObj.id}), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
              this.proRiskEval = response.data.proRiskEval;//下
              if (this.proRiskEval){
                  this.axios
                      .post(`/system/remote/getFileList/${this.proRiskEval.businessId}`)
                      .then(res =>{
                            if (res.code == "200"){
                                let fileList = res.data;
                                for(let i=0; i< fileList.length; i++){
                                    this.defaultList.push({
                                      url: fileList[i].fileId,
                                      name: fileList[i].filename
                                    });
                                }
                                this.$nextTick(() => {
                                    this.uploadList = this.$refs.upload.fileList;
                                });
                            }
                      })
                      .catch(function(error) {});
                      this.scoreObject['0'] = this.proRiskEval.totalScore;
                      this.scoreObject.riskLevel = this.proRiskEval.sysLevel;
                      this.proRiskScore.sysLevel = this.proRiskEval.sysLevel;
                      this.proRiskScore.confirmLevel = this.proRiskEval.confirmLevel;
                      this.proRiskScore.nextEvalDate = this.proRiskEval.nextEvalDate;
                      this.proRiskScore.remark = this.proRiskEval.remark;
                      this.proRiskScore.taskName = this.proRiskEval.taskName;
                      this.proRiskScore.nextUser = this.proRiskEval.nextUser;
                      this.remarkMust = this.proRiskEval.confirmLevel != this.proRiskEval.sysLevel;  //表单文本框是否是必输项校验
              }else {
                 this.$nextTick(() => {
                    this.uploadList = this.$refs.upload.fileList;
                });
              }
              let proInfo = response.data.proInfo;
              let evalId = response.data.evalId;
              let proEvalModel = response.data.proEvalModel;
              
              this.indicatorName = proEvalModel.modelName;
              this.proInfo = proInfo; //上
              this.proRiskScore.proId = proInfo.id;
              this.proRiskScore.evalId = evalId;
              this.proRiskScore.modelId = proEvalModel.id;
              this.proRiskScore.modelCode = proEvalModel.modelCode;

              let indicatorTemp = [];
              let indicator = response.data.indicator;
              for(let i=1; i<indicator.length; i++){
                  if (indicator[i].indicatorLevel == "1"){
                      indicatorTemp.push(indicator[i]);
                      this.panela.push(indicator[i].indicatorCode);
                  }else if (indicator[i].indicatorLevel == "2"){
                        this.panelb.push(indicator[i].indicatorCode);
                        if (indicatorTemp[indicatorTemp.length-1].children){
                            indicatorTemp[indicatorTemp.length-1].children.push(indicator[i]);
                        }else {
                          indicatorTemp[indicatorTemp.length-1].children = [];
                          indicatorTemp[indicatorTemp.length-1].children.push(indicator[i]);
                        }
                  }else if (indicator[i].indicatorLevel == "3") {
                      this.panelc.push(indicator[i].indicatorCode);
                      let ii = indicatorTemp[indicatorTemp.length-1].children.length;
                      if (indicatorTemp[indicatorTemp.length-1].children[ii-1].children){
                            indicatorTemp[indicatorTemp.length-1].children[ii-1].children.push(indicator[i]);
                        }else {
                          indicatorTemp[indicatorTemp.length-1].children[ii-1].children = [];
                          indicatorTemp[indicatorTemp.length-1].children[ii-1].children.push(indicator[i]);
                        }
                  }else if (indicator[i].indicatorLevel == "4") {
                      let ii = indicatorTemp[indicatorTemp.length-1].children.length;
                      let iii = indicatorTemp[indicatorTemp.length-1].children[ii-1].children.length;
                      if (indicatorTemp[indicatorTemp.length-1].children[ii-1].children[iii-1].children){
                            indicatorTemp[indicatorTemp.length-1].children[ii-1].children[iii-1].children.push(indicator[i]);
                        }else {
                          indicatorTemp[indicatorTemp.length-1].children[ii-1].children[iii-1].children = [];
                          indicatorTemp[indicatorTemp.length-1].children[ii-1].children[iii-1].children.push(indicator[i]);
                        }
                  }
                  if (indicator[i].hasSon == "0"){
                      let proEvalOptions = indicator[i].proEvalOptions;
                      let ooindex = -1
                      proEvalOptions.forEach((element, ind) => {
                          if (element.checked)
                            ooindex = ind;
                      });
                      let scoreDto = {
                        indicatorCode: indicator[i].indicatorCode,
                        riskBonus: indicator[i].riskBonus,
                        optId: ooindex == -1?"":proEvalOptions[ooindex].optId,
                        optName: ooindex == -1?"":proEvalOptions[ooindex].enums,
                        score: ooindex == -1?"":proEvalOptions[ooindex].score,
                        optRemark: ooindex == -1?"":proEvalOptions[ooindex].remark,
                        trueData: indicator[i].trueData,
                        detailRemark: indicator[i].detailRemark
                      };
                    this.proRiskScore.scoreDto.push(scoreDto);
                  }
              }
              this.indicator = indicatorTemp;
              this.$nextTick(() => {
                    this.panelA = this.panela;
                    this.panelB = this.panelb;
                    this.panelC = this.panelc;
              });
          }
        })
        .catch(error => {});
      }
        this.axios
        .post("/risk/pro/getRiskLevel")
        .then(response => {
            if (response.code == "200") {
                that.riskLevels = response.data;
                that.riskLevels.forEach((element, index) => {
                  this.message += "("+element.minValue+","+element.maxValue+"]为"+element.levelName;
                  if (index < that.riskLevels.length-1)
                      this.message += ";";
                });
            }
        })
        .catch(error => {});
        let url = `/bpm/getNodeDefDetail?bpmType=product_risk`;
        this.axios
        .get(url)
        .then((response) =>{
            let data = {
                bpmName: response.data[1].bpmName,
                nodeId:  response.data[1].nodeId,
                roleName: response.data[1].roleName
            };
            this.proRiskScore.bpmName = data.bpmName;
            data = JSON.stringify(data);
            this.axios
            .post("/bpm/getNextRole", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(res => {
              if (res.code == "200") {
                    this.data1 = res.data;
                    this.treeNLeafNodeDisable(this.data1);
              }
            })
            .catch(error => {});
      })
      .catch(function(error) {});
  },
  mounted(){
    console.log('mounted');
    this.$nextTick(() => {
      window.addEventListener('scroll', this.changeScrol, true); 
    })
  },
  destroyed() {
    console.log('destroyed');
    //离开该页面需要移除这个监听的事件
    window.removeEventListener("scroll", this.changeScrol,true);
  },
  watch:{
    "proRiskScore.confirmLevel": function(newValue, oldValue) {
      if (this.proRiskScore.sysLevel != null &&  this.proRiskScore.sysLevel != newValue) 
          this.remarkMust = true
      else this.remarkMust = false
      this.$nextTick(() => {
          this.$refs["formValidate"].validateField("remark");
      });
    },
    "proRiskScore.sysLevel": function(newValue, oldValue) {
       if (this.proRiskScore.confirmLevel != null &&  this.proRiskScore.confirmLevel != newValue) 
          this.remarkMust = true
      else this.remarkMust = false
      this.$nextTick(() => {
          this.$refs["formValidate"].validateField("remark");
      });
    }
  },
  data() {
     const validaterNextUser = (rule, value, callback) => {
      if (this.proRiskScore.nextUser == "" && this.modal1) {
        return callback(new Error("请至少选择一个审核人"));
      } else {
        callback();
      }
    };
    return {
      positonFix:false,
      options1: {
          disabledDate (date) {
              return date && date.valueOf() < Date.now()-24*60*60*1000;
          }
      },
      message: "",
      handleSaveDisable:false,
      handleSubmitDisable: false,
      thead:[
        {'name':'序号'},
        {'name':'项目（权重%）'},
        {'name':'内容'},
        {'name':'得分'},
        {'name':'指标类型'},
        {'name':'具体数据（金额单位：元）'},
        {'name':'备注说明'},
      ],
      defaultList: [
      ],
      errorList: [],
      remarkMust: false,
      data1: [],
      modal1: false,
      modal2: false,
      riskLevels: [],
      scoreObject: {
      },
      uploadList: [],
      firstScore: true,
      indicatorName: null,
      proInfo: {},
      proRiskEval: {},
      proRiskScore: {
        proId: "",
        evalId: "",
        modelId: "",
        modelCode: "",
        scoreDto: [],
        sysLevel: null,
        confirmLevel: null,
        nextEvalDate: "",
        remark: "",
        bpmName: "",
        nextUser: "",
        taskName:""
      },
      evalId: "",
      indicator: [],
      panelA: [],
      panela: [],
      panelB: [],
      panelb: [],
      panelC: [],
      panelc: [],
      columns1: [
        {
          title: '文件名',
          key: 'name'
        },
        {
            title: '操作',
            key: '',
            width: 150,
            align: 'center',
            render: (h, params) => {
                return h('div', [
                    h('Button', {
                        props: {
                            type: 'primary',
                            size: 'small'
                        },
                        style: {
                            marginRight: '5px'
                        },
                        on: {
                            click: () => {
                                this.fileDownload(params.index, params.row.name)
                            }
                        }
                    }, '下载'),
                    h('Button', {
                        props: {
                            type: 'error',
                            size: 'small'
                        },
                        on: {
                            click: () => {
                                this.remove(params.index)
                            }
                        }
                    }, '删除')
                ]);
            }
        }
      ],
      ruleValidate: {
        confirmLevel: [
          {
            required: true,
            message: "请输入确认等级",
            trigger: "change"
          }
        ],
        nextUser: [
          {
            required: false,
            validator: validaterNextUser,
            trigger: "on-check-change"
          }
        ],
         remark: [
          {
            required: true,
            message: "评估备注不能为空",
            trigger: "blur"
          },
          {
            type: "string",
            max: 500,
            message: "长度最大不能超过500",
            trigger: "blur"
          }
        ],
        taskName:[
          {
            required: true,
            message: "流程标题不能为空",
            trigger: "blur"
          },
          {
            type: "string",
            max: 50,
            message: "流程标题不能超过50个字",
            trigger: "blur"
          }
        ],
        remark2:[{required: false, max: 500,message: '长度最大不能超过500',trigger: 'blur'}]
      }
    };
  },
  methods: {
    changeScrol() {
      /**
       * getBoundingClientRect().top 获取某元素距离浏览器顶部的高度，不包含滚动的距离
        this.offsetTop 表示的是吸顶元素距离顶部的条件值（一般项目需求是0）
       */
      let targetOffsetTop = this.$refs.target.getBoundingClientRect().top;
      let historyOffsetTop = this.$refs.history.getBoundingClientRect().top;
      console.log('targetOffsetTop', targetOffsetTop);
      console.log('historyOffsetTop', historyOffsetTop);
      if(targetOffsetTop < 200){
        this.positonFix = true;
        if(historyOffsetTop < 220){
          this.positonFix = false;
        }
      }else{
        this.positonFix = false;
      }
    },
    handleSave(name) {
      let valid1 = true;
      this.$refs[name].validate(valid => {
        valid1 = valid;
      });
      this.errorList = [];
      this.proRiskScore.scoreDto.forEach(element => {
        if (element.optId == "" && element.riskBonus != "1") {
            this.errorList.push(
              {
                message: '编号：'+element.indicatorCode+'           ====>>内容不能为空'
              }
            );
        }
      });
      if (valid1) {
          if (this.errorList.length != 0){
            this.modal2 = true;
            return;
          }
          this.handleSaveDisable=true;
          this.proRiskScore.fileName = [];
          this.proRiskScore.filePath = [];
          this.uploadList.forEach(element => {
            this.proRiskScore.fileName.push(element.name);
            this.proRiskScore.filePath.push(element.url);
          });
          let data = JSON.parse(JSON.stringify(this.proRiskScore));
          data.fileName = JSON.stringify(
            data.fileName
          );
          data.filePath = JSON.stringify(
            data.filePath
          );
          data = JSON.stringify(data);
          this.axios
            .post("/risk/pro/eval/saveEvalDetail", data, {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                this.$Message.success("保存成功");
                setTimeout(()=>{
                     this.$emit("save");
                },2000)
              }else {
                this.$Message.error("保存失败");
                this.handleSaveDisable=false;
              }
            })
            .catch(error => {});
      }
    },
    handleSubmit(name){
        this.$refs[name].validate(valid => {
          if (valid) {
            this.handleSubmitDisable=true;
            this.proRiskScore.fileName = [];
            this.proRiskScore.filePath = [];
            this.uploadList.forEach(element => {
              this.proRiskScore.fileName.push(element.name);
              this.proRiskScore.filePath.push(element.url);
            });
            let data = JSON.parse(JSON.stringify(this.proRiskScore));
            data.fileName = JSON.stringify(
              data.fileName
            );
            data.filePath = JSON.stringify(
              data.filePath
            );
            data = JSON.stringify(data);
            this.axios
              .post("/risk/pro/eval/evalTask", data, {
                headers: { "Content-Type": "application/json" }
              })
              .then(response => {
                if (response.code == "200") {
                  this.$Message.success("保存成功");
                   setTimeout(()=>{
                     this.$emit("save");
                     this.modal1 = false;
                    },2000)
                }else {
                  this.$Message.error("保存失败");
                  this.handleSubmitDisable=false;
                }
              })
              .catch(error => {});
          }
      });
    },
    treeNLeafNodeDisable(treeDatas){
        for(let i=0;i<treeDatas.length;i++){
            if (treeDatas[i].children){
                this.$set(treeDatas[i], "disabled", true);
                this.$set(treeDatas[i], "expand", true);
                this.treeNLeafNodeDisable(treeDatas[i].children);
            }
        }
    },
     updateNextUser(nodes, node) {
        nodes.forEach(el => {
            if (el.nodeKey != node.nodeKey){
                el.checked = false;
            }
          });
          this.proRiskScore.nextUser = "";
          nodes.forEach(element => {
            if (element.checked==true)
              this.proRiskScore.nextUser = element.code;
          });
          this.$refs["formValidate"].validateField("nextUser");
    },
    next(name) {
      this.errorList = [];
      this.proRiskScore.scoreDto.forEach(element => {
        if (element.optId == "" && element.riskBonus != "1") {
            this.errorList.push(
              {
                message: '编号：'+element.indicatorCode+'           ====>>内容不能为空'
              }
            );
        }
      });
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.errorList.length != 0){
            this.modal2 = true;
            return;
          }
          this.modal1 = true;
        }
      });
    },
    handleFormatError (file) {
        this.$Notice.warning({
            title: '附件格式错误',
            desc: '请选择zip、rar、xls、xlsx、doc、docx、pdf.'
        });
    },
    handleMaxSize (file) {
        this.$Notice.warning({
            title: '超出了附件大小限制',
            desc: ' 不应超过10M'
        });
    },
    remove(index){
      let filePath = this.uploadList[index].url;
      this.axios
      .post(`/system/remote/delFile/${filePath}`)
      .then(response => {
          if ((response = true)) {
              this.uploadList.splice(index, 1);
              this.$Message.success("删除成功");
          }else {
              this.$Message.error("删除失败");
          }
      })
    },
    handleUploadfile(file){
       const check = this.uploadList.length < 5;
        if (!check) {
            this.$Notice.warning({
                title: '只能上传最多五个附件.'
            });
        }
        return check;
    },
    handleSuccess(response, file) {
      if (response.code == "200") {
          file.url = response.data[0].fileId;
      }
    },
    fileDownload(index, fileName) {
      let that = this;
      let filePath = this.uploadList[index].url;
      let data = JSON.stringify({
        token: "",
        fileId: filePath
      });
      this.axios
        .post("/system/remote/micDownLoad", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          let data2 = JSON.stringify({
            url: response.message,
            fileName: fileName
          });
          this.axios
            .post(`/system/remote/getFileInfo`, data2, {
              headers: { "Content-Type": "application/json" },
              responseType: "blob"
            })
            .then(res => {
              this.download(res, fileName);
            })
            .catch(function(error) {});
        })
        .catch(error => {});
    },
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    handleError() {
      this.$Message.error("上传失败");
    },
    monopo(list, indexPara, indicatorCode){
        list.forEach((element, index) => {
            if (index == indexPara) {
                element.checked = true;
                this.proRiskScore.scoreDto.forEach(e2 => {
                  if (e2.indicatorCode == indicatorCode){
                      e2.optId = element.optId;
                      e2.optName = element.enums;
                      e2.score = element.score;
                      e2.optRemark = element.remark;
                  }
                });
            }else element.checked = false;
        });
        this.sendProRiskScore();
    },
    sendProRiskScore(){
        this.axios
            .post("/risk/pro/eval/getProScore ", JSON.stringify(this.proRiskScore), {
              headers: { "Content-Type": "application/json" }
            })
            .then(response => {
              if (response.code == "200") {
                  this.scoreObject = response.data;
                  this.proRiskScore.sysLevel = this.scoreObject.riskLevelCode;
                  this.firstScore = false;
              }else {
                that.$Message.error(response.message);
              }
            })
            .catch(error => {});
    },
    updatetrueData(indicatorCode, trueData){
        this.proRiskScore.scoreDto.forEach(element => {
            if (element.indicatorCode == indicatorCode){
                element.trueData = trueData;
            }
        });
    },
    updatedetailRemark(indicatorCode, detailRemark){
        this.proRiskScore.scoreDto.forEach(element => {
            if (element.indicatorCode == indicatorCode){
                element.detailRemark = detailRemark;
            }
        });
    },
    handleCancel(name) {
      this.$emit("cancel", false);
    }
  }
};
</script>
<style lang="scss">
.assess-risk {
  .content {
    padding: 0 20px 20px;
    .title {
      text-align: center;
      font-size: 18px;
      font-weight: bold;
    }
    .collapse-content {
      .tableBox>thead tr{
          background-color:#F6FCFD;
          color: #333;
        }
      .content1 {
        line-height: 40px;
        height: 40px;
        background: #019FE8;
        span {
          font-size: 14px;
          &:nth-child(1) {
            margin-left: 20px;
            color: #fff;
          }
          &:nth-child(4) {
            color: #fff;
            float: right;
            font-size: 35px;
            i {
              margin-top: -5px;
              cursor: pointer;
            }
            .api {
              color: #333;
              font-size: 14px;
            }
          }
          &:nth-child(3) {
            float: right;
            font-size: 16px;
            color: #fff;
            margin-right: 20px;
          }
          &:nth-child(2) {
            font-weight: bold;
            font-size: 16px;
            color: #fff;
            float: right;
            margin-right: 50px;
          }
        }
      }
      .buss-title {
        font-weight: bold;
        color: #333;
        font-size: 14px;
      }
    }
    .form-content {
      .speical-span{
        width: 8.33333333%;
        float: left;
      }
      label{
        font-size: 14px;
        i{
          font-size: 24px;
          color: #87B8FF;
          margin-right: 5px;
          vertical-align: -5px;
        }
        &.special-label{
          color: #87B8FF;
        }
      }
      .ivu-form-item-label {
        font-size: 14px !important;
        text-align: left;
      }
      .container{
        padding: 20px;
        margin: 20px 0;
        border: 0.5px solid #ebecee;
      }
    }
    .tab-desc {
      color: #fff;
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      background: #019FE8;
      padding-left: 20px;
      span {
        &:nth-child(2) {
          margin-left: 20px;
        }
      }
    }
    table {
      width: 100%;
      font-size: 16px;
      text-align: center;
      color: #333333;
      margin-bottom: 20px;
      &.first-tab {
        tr{
          height: 41px;
          td {
            height: 40px;
            line-height: 40px;
            width: 35%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            padding: 0 5px;
            background: #FDFDFD;
            &:nth-child(odd) {
              font-size: 14px;
              background: #DEF1FF;
              width:15%;
            }
            &.process-detail {
              color: #0000cc;
              cursor: pointer;
              text-align: center;
            }
          }
        }
      }
      &.second-tab {
        thead {
          tr {
            td {
              font-weight: bold;
            }
          }
        }
        tbody {
          td {
            &:nth-child(odd) {
              font-weight: normal;
            }
          }
        }
      }
      &.third-tab{
        thead{
          tr{
            padding-bottom: 0;
            font-size: 14px;
            display: inline-block;
            line-height: 50px;
            td{
              text-align: center;
            }
          }
        }
        tr{
          font-size: 13px;
          display: inline-block;
          padding-bottom: 10px;
          height: auto;
          td{
            text-align: left;
            &:nth-child(1){
              width: 50px;
            }
            &:nth-child(2){
              width: 250px;
            }
            &:nth-child(3){
              width: 300px;
            }
            &:nth-child(4){
              width: 60px;
            }
            &:nth-child(5){
              width: 50px;
              text-align: center;
            }
            &:nth-child(6){
              width: 200px;
            }
            &:nth-child(7){
              width: 200px;
            }
          }

        }
      }
    }
  }
  .ivu-collapse-content{
    padding-left: 0;
  }
  .ivu-collapse{
    margin-bottom: 10px;
  }
  .ivu-collapse-content>.ivu-collapse-content-box{
    padding-bottom: 0;
  }
  .totalPercent{
    color: red;
    font-size: 14px;
    margin-left: 20px;
  }
}
</style>


