<template>
  <div class="second-step part-seconde-step">
    <div class="content">
      <div class="table-info">
        <span>评估部门/单位：{{receiveDatas.orgName}}</span>
        <span>评估期间：{{receiveDatas.evalPeriod}}~{{receiveDatas.evalPeriod1}}</span>
      </div>
      <div class="target" ref="target">
        <span>指标</span>
        <Button
          v-for="(item,index) in activeButtoArr"
          :key="index"
          @click="activeButtoFun(item)"
        >{{item}}级</Button>
      </div>
      <div class="tab-desc">
        <span>{{indicatorName}}</span>
        <span>总分：{{scoreObject.evalScore}}分</span>
        <span style="margin-left:20px">风险等级：{{scoreObject.sysLevel}}</span>
      </div>
      <table class="tableBox" cellspacing="0" cellpadding="0" style="margin-bottom:0;">
        <thead :class="positonFix?' positon-fix pos-fix-thead':''">
          <tr>
            <td class="p2">项目</td>
            <td class="p3">权重</td>
            <td class="p4">系统指标内容</td>
            <td class="p5">确认指标内容</td>
            <td class="p6">指标说明</td>
            <td class="p7">
              <span>评分</span> 
              <span>
                <Poptip placement="top" width="400" color="#666" trigger="hover">
                    <Icon type="ios-information" />
                    <div class="api" slot="content">
                        指标分值调整需联系相关人员在模型设置中进行修改
                    </div>
                </Poptip>
            </span>
            </td>
            <td class="p8">指标类型</td>
            <td class="p9">具体数据</td>
            <td class="p10">基准数据</td>
            <td class="p11">比例</td>
            <td class="p12">填写备注</td>
            <td class="p13">附件</td>
          </tr>
        </thead>
      </table>
      <div class="CollapseBox">
        <el-collapse v-model="activeNames" @change="handleChange">
          <template v-for="item in activeArr">
            <el-collapse-item :name="item.indNum" :key="item.indNum">
              <template slot="title">
                <p class="p2 ppadding">
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="item.indicator.indicatorName"
                  >
                    <span
                      slot="reference"
                      class="textOverflow textMaxWidth"
                    >{{item.indNum}}{{item.indicator.indicatorName}}</span>
                  </el-popover>
                </p>
                <p class="p3 ppadding">{{item.indicator.weight}}</p>
                <p
                  class="p4 ppadding textOverflow textMaxWidth2"
                >{{item.indicator.sysContent}}</p>
                <p class="p5 ppadding"></p>
                <p class="p6 ppadding textOverflow textMaxWidth2"></p>
                <p class="p7 ppadding"></p>
                <p
                  class="p8 ppadding textOverflow textMaxWidth2"
                >{{item.indicator.indicatorType}}</p>

                <p class="p9 ppadding">
                  <Input
                    v-show="item.indicator.indicatorLevel == 4"
                    v-model="item.indicator.specificData"
                    @on-change="updatespecificData(item.indicator.indicatorCode,item.indicator.specificData,item.indicator.basicValue)"
                  ></Input>
                </p>
                <p class="p10 ppadding"></p>
                <p
                  class="p11 ppadding textOverflow textMaxWidth2"
                >{{item.indicator.ratio}}</p>
                <p class="p12 ppadding"></p>
                <p class="p13 ppadding"></p>
              </template>
              <template v-if="item.sonVo.length>0?true:false">
                <el-collapse-item
                  v-for="(item2,index2) in item.sonVo"
                  :key="item2.indNum"
                  :name="item2.indNum"
                >
                  <template slot="title">
                    <p class="p2">
                      <el-popover
                        placement="top-start"
                        title
                        width="200"
                        trigger="hover"
                        :content="item2.indicator.indicatorName"
                      >
                        <span
                          slot="reference"
                          class="textOverflow textMaxWidth"
                        >{{item2.indNum}}{{item2.indicator.indicatorName}}</span>
                      </el-popover>
                    </p>
                    <p class="p3 ppadding">{{item2.indicator.weight}}</p>
                    <p
                      class="p4 ppadding textMaxWidth2 textOverflow"
                    >{{item2.indicator.sysContent}}</p>
                    <p class="p5 ppadding"></p>
                    <p class="p6 ppadding textMaxWidth2 textOverflow"></p>
                    <p class="p7 ppadding"></p>
                    <p
                      class="p8 ppadding textMaxWidth2 textOverflow"
                    >{{item2.indicator.indicatorType}}</p>

                    <p class="p9 ppadding"></p>
                    <p class="p10 ppadding"></p>
                    <p
                      class="p11 ppadding textMaxWidth2 textOverflow"
                    >{{item2.indicator.ratio}}</p>

                    <p class="p12 ppadding"></p>
                    <p class="p13 ppadding"></p>
                  </template>
                  <template v-if="item2.sonVo.length>0?true:false">
                    <el-collapse-item
                      v-for="(item3,index3) in item2.sonVo"
                      :key="item3.indNum"
                      :name="item3.indNum"
                    >
                      <template slot="title">
                        <p class="p2">
                          <el-popover
                            placement="top-start"
                            title
                            width="200"
                            trigger="hover"
                            :content="item3.indicator.indicatorName"
                          >
                            <span
                              slot="reference"
                              class="textOverflow textMaxWidth"
                            >{{item3.indNum}}{{item3.indicator.indicatorName}}</span>
                          </el-popover>
                        </p>
                        <p class="p3 ppadding">{{item3.indicator.weight}}</p>
                        <p
                          class="p4 ppadding textMaxWidth2 textOverflow"
                        >{{item3.indicator.sysContent}}</p>
                        <p class="p5 ppadding"></p>
                        <p class="p6 ppadding textMaxWidth2 textOverflow"></p>
                        <p class="p7 ppadding"></p>
                        <p
                          class="p8 ppadding textMaxWidth2 textOverflow"
                        >{{item3.indicator.indicatorType}}</p>

                        <p class="p9 ppadding"></p>
                        <p class="p10 ppadding"></p>
                        <p class="p11 ppadding">{{item3.indicator.ratio}}</p>

                        <p class="p12 ppadding"></p>
                        <p class="p13 ppadding"></p>
                      </template>
                      <template v-if="item3.sonVo.length>0?true:false">
                        <el-collapse-item
                          v-for="(item4,index4) in item3.sonVo"
                          :key="item4.indNum"
                          :name="item4.indNum"
                        >
                          <template slot="title">
                            <p class="p2">
                              <el-popover
                                placement="top-start"
                                title
                                width="200"
                                trigger="hover"
                                :content="item4.indicator.indicatorName"
                              >
                                <span
                                  slot="reference"
                                  class="textOverflow textMaxWidth"
                                >{{item4.indNum}}{{item4.indicator.indicatorName}}</span>
                              </el-popover>
                            </p>
                            <p class="p3 ppadding">{{item4.indicator.weight}}</p>
                            <p
                              class="p4 ppadding textMaxWidth2 textOverflow"
                            >{{item4.indicator.sysContent}}</p>
                            <p class="p5 ppadding">
                              <Select placeholder="请选择" v-model="item4.indicator.optId" @on-change="changeSts($event,item4)">
                                <Option
                                  v-for="(item7,index) in item4.indicator.orgOptions"
                                  :value="item7.optId"
                                  :key="item7.optId"
                                >{{item7.enums}}</Option>
                              </Select>
                            </p>
                            <p class="p6 ppadding">{{item4.indicator.remark}}</p>
                            <p class="p7 ppadding">{{item4.indicator.defaultValue}}</p>
                            <p
                              class="p8 ppadding textMaxWidth2 textOverflow"
                            >{{item4.indicator.indicatorType}}</p>

                            <p class="p9 ppadding">
                              <Input
                                v-model="item4.indicator.specificData"
                                @on-change="updatebasicValue(item4.indicator.indicatorCode,item4.indicator.basicValue,item4.indicator.specificData,item4)"
                              ></Input>
                            </p>
                            <p class="p10 ppadding">
                              <Input
                                v-model="item4.indicator.basicValue"
                                @on-change="updatebasicValue(item4.indicator.indicatorCode,item4.indicator.basicValue,item4.indicator.specificData,item4)"
                              ></Input>
                            </p>
                            <p class="p11 ppadding">{{item4.indicator.ratio}}</p>
                            <p class="p12 ppadding">
                              <Input v-model="item4.indicator.detailRemark"></Input>
                            </p>
                            <p
                              class="p13 ppadding"
                              @click="watchFiles(item4,item4.indicator.indicatorCode)"
                              style="color: #3399ff"
                            >上传</p>
                          </template>
                        </el-collapse-item>
                      </template>
                    </el-collapse-item>
                  </template>
                </el-collapse-item>
              </template>
            </el-collapse-item>
          </template>
        </el-collapse>
      </div>
    </div>
    <Modal :mask-closable="false"  
      v-model="showModal"
      :title="modelTitle"
      @on-cancel="cancel"
      class="dialog"
      :styles="{width: percentWidth}"
    >
      <component
        :is="tabview"
        :file-obj="fileObj"
        :key="fileId"
        @requestUploadList="receiveUploadList"
      ></component>
      <div slot="footer"></div>
    </Modal>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: {
    receiveDatas: Object
  },
  created() {
    console.log("receiveDatas", this.receiveDatas);
    this.getOrgEvalDetail(this.receiveDatas.id);
    this.getDetailByLevelNum();
  },
  components: { showUsualFiles },
  data() {
    return {
      positonFix:false,
      name: "partsecondstep",
      indicatorName: null,
      scoreObject: {},//总分和风险等级
      percentWidth: "60%",
      fileId: "",
      modelTitle: "新增",
      tabview: "showUsualFiles",
      showModal: false,
      fileObj: {},
      uploadList: {
        fileName: [],
        filePath: []
      },
      fileCode: "",
      orgRiskScore: {
        sysLevel: null,
        orgId: "",
        modelId: "",
        modelCode: "",
        scoreDto: []
      },
      defaultList: [],
      activeNames: [],
      activeButtoArr: [],
      activeArr: [], //手风琴盒子回显数组+
      tableHeader: [
        { name: "项目" },
        { name: "权重%" },
        { name: "系统指标内容" },
        { name: "确认指标内容" },
        { name: "指标说明" },
        { name: "评分" },
        { name: "指标类型" },
        { name: "具体数据" },
        { name: "基准数据" },
        { name: "比例" },
        { name: "填写备注" },
        { name: "附件" }
      ]
    };
  },
  mounted(){
    console.log('mounted');
    this.$nextTick(() => {
      window.addEventListener('scroll', this.changeScrol, true); 
    })
  },
  destroyed() {
    console.log('destroyed');
    //离开该页面需要移除这个监听的事件
    window.removeEventListener("scroll", this.changeScrol,true);
  },
  methods: {
    changeScrol() {
      /**
       * getBoundingClientRect().top 获取某元素距离浏览器顶部的高度，不包含滚动的距离
        this.offsetTop 表示的是吸顶元素距离顶部的条件值（一般项目需求是0）
       */
      let targetOffsetTop = this.$refs.target.getBoundingClientRect().top;
      console.log('targetOffsetTop', targetOffsetTop);
      if(targetOffsetTop < 200){
        this.positonFix = true;
      }else{
        this.positonFix = false;
      }
    },
    changeSts(val, item) {
      item.indicator.orgOptions.forEach((orgOption, index) => {
        if (orgOption.optId == val) {
          item.indicator.optId = val;
          item.indicator.optRemark = orgOption.optRemark;
          item.indicator.remark = orgOption.optRemark;
          item.indicator.defaultValue = orgOption.score;
          item.indicator.confContent = orgOption.enums;
          this.$forceUpdate();
          try {
            throw Error();
          } catch (error) {
            console.log("跳出");
          }
        }
      });
      this.sendBusRiskScore();
      console.log("item.indicator", item.indicator);
    },
    // 固有风险评估手风琴
    getDetailByLevelNum() {
      this.axios
        .post("/risk/org/getDetailByLevelNum")
        .then(response => {
          if (response.code == "200") {
            this.activeButtoArr = response.data;
            this.activeButtoFun('4');
          }
        })
        .catch(error => {});
    },
    //获取手风琴数据
    getOrgEvalDetail(id) {
      this.axios
        .post(`/risk/org/getOrgEvalDetail?id=${id}`)
        .then(response => {
          if (response.code == "200") {
            this.activeArr = response.data.indicatorVo;
            let orgInfo = response.data.orgInfo;
            let orgEvalModel = response.data.orgEvalModel;
            if (orgInfo) {
              this.scoreObject.evalScore= orgInfo.evalScore;
              this.scoreObject.sysLevel = orgInfo.evalLevel;
            }
            this.indicatorName = orgEvalModel.modelName;
            this.orgRiskScore.orgId = this.receiveDatas.id;
            this.orgRiskScore.modelId = orgEvalModel.id;
            this.orgRiskScore.modelCode = orgEvalModel.modelCode;
          }
        })
        .catch(error => {});
    },
    handleSave(param) {
      this.orgRiskScore.scoreDto = [];
      this.showTree(this.activeArr);
      console.log('this.orgRiskScore.scoreDto', this.orgRiskScore.scoreDto);
      this.axios
        .post("/risk/org/saveOrgEvalDetail", this.orgRiskScore, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
              if(param != 'nextStep'){
                this.$Message.success("保存成功");
              }
          } else {
            this.$Message.error("保存失败");
          }
        })
        .catch(error => {});
    },
    sendBusRiskScore() {
      this.orgRiskScore.scoreDto = [];
      this.showTree(this.activeArr);
      console.log('this.orgRiskScore.scoreDto', this.orgRiskScore.scoreDto);
      this.axios
        .post("/risk/org/getOrgRiskSorce", JSON.stringify(this.orgRiskScore), {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.scoreObject.evalScore = response.data[0];
            this.scoreObject.sysLevel = response.data.riskLevel;
            this.$forceUpdate();
          } else {
            this.$Message.error(response.message);
          }
        })
        .catch(error => {});
    },
    getFileTree(datas, code, uploadList) {
      datas.forEach(data => {
        if (data.indicator.hasSon == "1") {
          if (data.indicator.indicatorCode == code) {
            data.indicator.filePath = JSON.stringify(uploadList.filePath);
            data.indicator.fileName = JSON.stringify(uploadList.fileName);
            console.log("this.activeArr", this.activeArr);
            try {
              throw Error();
            } catch (error) {
              console.log("跳出");
            }
          } else {
            this.getFileTree(data.sonVo, code, uploadList);
          }
        } else {
          if (data.indicator.indicatorCode == code) {
            data.indicator.filePath = JSON.stringify(uploadList.filePath);
            data.indicator.fileName = JSON.stringify(uploadList.fileName);
            console.log("this.activeArr", this.activeArr);
            try {
              throw Error();
            } catch (error) {
              console.log("跳出");
            }
          }
        }
      });
    },
    showTree(treeDatas) {
      for (let i = 0; i < treeDatas.length; i++) {
        if (treeDatas[i].indicator.hasSon == "1") {
          this.showTree(treeDatas[i].sonVo);
        } else {
          if(treeDatas[i].indicator.indicatorCode.length == 7){
            let scoreDto = {
              baseData:treeDatas[i].indicator.basicValue,
              confContent: treeDatas[i].indicator.confContent,
              detailRemark: treeDatas[i].indicator.detailRemark,
              filePath: treeDatas[i].indicator.filePath,
              fileName: treeDatas[i].indicator.fileName,
              indicatorCode: treeDatas[i].indicator.indicatorCode,
              optId: treeDatas[i].indicator.optId,
              optRemark:treeDatas[i].indicator.optRemark,
              ratio: treeDatas[i].indicator.ratio,
              score: treeDatas[i].indicator.defaultValue,
              sysContent: treeDatas[i].indicator.sysContent,
              trueData: treeDatas[i].indicator.specificData,
            };
            this.orgRiskScore.scoreDto.push(scoreDto);
          }
        }
      }
    },
    cancel() {},
    updatespecificData(indicatorCode, specificData, basicValue, item) {
      this.orgRiskScore.scoreDto.forEach(element => {
        if (element.indicatorCode == indicatorCode) {
          element.specificData = specificData;
        }
      });
      let data = {
        indicatorCode: indicatorCode,
        trueData: specificData,
        baseData: basicValue
      };
      data = JSON.stringify(data);
      this.axios
        .post("/risk/org/getOptionsByRatio", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            item.indicator.orgOptions = response.data.options;
            item.indicator.ratio = response.data.ratio;
            this.orgRiskScore.scoreDto.forEach(element => {
              if (element.indicatorCode == indicatorCode) {
                element.ratio = response.data.ratio;
              }
            });
          }
        })
        .catch(error => {});
    },
    updatebasicValue(indicatorCode, basicValue, specificData, item) {
      if (basicValue && specificData) {
        let data = {
          indicatorCode: indicatorCode,
          trueData: specificData,
          baseData: basicValue
        };
        data = JSON.stringify(data);
        this.axios
          .post("/risk/org/getOptionsByRatio", data, {
            headers: { "Content-Type": "application/json" }
          })
          .then(response => {
            if (response.code == "200") {
              item.indicator.orgOptions = response.data.options;
              item.indicator.ratio = response.data.ratio;
            }
          })
          .catch(error => {});
          this.sendBusRiskScore();
      }
    },
    updatedetailRemark(indicatorCode, detailRemark) {
      this.orgRiskScore.scoreDto.forEach(element => {
        if (element.indicatorCode == indicatorCode) {
          element.detailRemark = detailRemark;
        }
      });
    },
    receiveUploadList(list) {
      this.uploadList = {
        fileName: [],
        filePath: []
      };
      list.forEach(item => {
        this.uploadList.filePath.push(item.url);
        this.uploadList.fileName.push(item.name);
      });
      this.getFileTree(this.activeArr, this.fileCode, this.uploadList);
      console.log("this.activeArr", this.activeArr);
    },
    watchFiles(item, code) {
      this.fileCode = code;
      this.percentWidth = "40%";
      this.modelTitle = "文件列表";
      this.tabview = "showUsualFiles";
      this.showModal = true;
      this.fileId = item.indicator.indicatorCode;
      if (item.indicator && item.indicator.fileName) {
        let fileName = JSON.parse(item.indicator.fileName);
        let filePath = JSON.parse(item.indicator.filePath);
        let fileList = [];
        for (let i = 0; i < fileName.length; i++) {
          fileList.push({
            url: filePath[i],
            name: fileName[i]
          });
        }
        this.fileObj = {
          data: item,
          showFlag: true,
          fileList: fileList
        };
      } else {
        item.busId = item.indicator.fileBussiness;
        this.fileObj = {
          data: item,
          showFlag: true
        };
      }
    },
    activeButtoFun(a) {
      let that = this;
      this.axios
        .post(`/risk/org/getDetailByLevel/${a}`)
        .then(response => {
          if (response.code == "200") {
            that.activeNames = response.data;
          }
        })
        .catch(error => {});
    },
    handleChange() {}
  }
};
</script>
<style lang="scss" scoped>
.CollapseBox .el-collapse-item__content {
  padding-bottom: 0 !important;
}
.tableBox > thead tr {
  min-height: 10px;
}
.tableBox > thead tr td {
  border: none;
}
.textmaxwidth {
  max-width: 200px;
}
.part-seconde-step .p1 {
  width: 5%;
  float: left;
  text-align: left;
  height: 60px;
  line-height: 40px;
  box-sizing: border-box;
}
.part-seconde-step .p2 {
  margin-left: 2%;
  width: 10%;
  float: left;
  text-align: left;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
  white-space: nowrap;
  padding: 0 !important;
  overflow: hidden;
  text-overflow: ellipsis;
}
.part-seconde-step .p3 {
  width: 7%;
  float: left;
  text-align: left;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.ppadding {
  padding-left: 20px;
}
.part-seconde-step .p4 {
  width: 8%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p5 {
  width: 10%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p6 {
  width: 12%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p7 .ivu-icon-ios-information:before{
  font-size: 30px;
  color: red;
  margin-left: -5px;
}
.part-seconde-step .p7 {
  width: 5%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p8 {
  width: 7%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p9 {
  width: 7%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p10 {
  width: 7%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p11 {
  width: 7%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p12 {
  width: 11%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.part-seconde-step .p13 {
  width: 7%;
  float: left;
  text-align: center;
  height: 60px;
  line-height: 50px;
  box-sizing: border-box;
}
.CollapseBox {
  width: 100%;
  min-height: 200px;
}
.second-step {
  margin-bottom: 40px;
  .content {
    .table-info {
      font-size: 14px;
      margin: 20px auto 40px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .target {
      text-align: right;
      margin-bottom: 10px;
      button {
        margin-left: 20px;
        background: #1c67ce;
        color: #fff;
        span {
          color: #fff;
        }
      }
      span {
        font-size: 14px;
        color: #333;
      }
    }
    .tab-desc {
      color: #fff;
      font-size: 14px;
      height: 40px;
      line-height: 40px;
      background: #019fe8;
      padding-left: 20px;
      span {
        &:nth-child(2) {
          margin-left: 40px;
        }
      }
    }
    .tableBox {
      margin-top: 1px;
    }
  }
}
</style>


